import React, { Component } from "react";
import Table from "../../components/common/table";

class StoresTable extends Component {
  columns = [
    { path: "description", lable: "Description" },
    { path: "location", lable: "Location" },
    { path: "contact", lable: "Contact" },
    { path: "officephone", lable: "OfficePhone" },
    {
      key: "edit",
      lable: "Actions",
      content: store => (
        <React.Fragment>
          <div className="row">
            <button
              className="btn btn-sm btn-primary mr-2 ml-3"
              onClick={() => this.props.onEdit(store)}
            >
              <i className="cui-pencil icons font-lg" />
            </button>

            <button
              className="btn btn-sm btn-danger"
              onClick={() => this.props.onDelete(store)}
            >
              <i className="cui-trash icons font-lg " />
            </button>
          </div>
        </React.Fragment>
      )
    }
  ];
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const { stores, onSort, onSearch, sortColumn, allStores } = this.props;

    return (
      <Table
        columns={this.columns}
        sortColumn={sortColumn}
        onSort={onSort}
        onSearch={onSearch}
        data={stores}
        allData={allStores}
      />
    );
  }
}

export default StoresTable;
