import React, { Component } from "react";
import Table from "../../components/common/table";

class SupplierOrdersTable extends Component {
  columns = [
    { path: "id", lable: "#" },
    { path: "status", lable: "Status" },
    { path: "createdAt", lable: "Created at" },
    { path: "containerPosition", lable: "Container Position" }
  ];
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const {
      purchases,
      onSort,
      onSearch,
      sortColumn,
      allPurchases
    } = this.props;

    return (
      <Table
        columns={this.columns}
        sortColumn={sortColumn}
        onSort={onSort}
        onSearch={onSearch}
        data={purchases}
        allData={allPurchases}
      />
    );
  }
}

export default SupplierOrdersTable;
