import React, { Component } from "react";
import Table from "../../components/common/table";
import { Button, Popup } from "semantic-ui-react";

const getBadge = status => {
  if (status === null) {
    return "badge badge-warning";
  }
  return status === 3
    ? "badge badge-success"
    : status === 2
    ? "badge badge-secondary"
    : status === 1
    ? "badge badge-warning"
    : status === 4
    ? "badge badge-primary"
    : status === 5
    ? "badge badge-danger"
    : "badge badge-warning";
};

const getStatusLabel = statusId => {
  return statusId === 1
    ? "initiated"
    : statusId === 2
    ? "verified"
    : statusId === 3
    ? "approved"
    : statusId === 4
    ? "executed"
    : statusId === 5
    ? "cancelled"
    : "initiated";
};

class SalesTable extends Component {
  columns = [
    { path: "createdAt", lable: "Sales Date" },
    { path: "clientNames", lable: "Client" },
    {
      key: "stat",
      content: sale => (
        <span className={getBadge(sale.statusId)}>
          {getStatusLabel(sale.statusId)}
        </span>
      ),
      lable: "Status"
    },
    {
      key: "edit",
      lable: "Actions",
      content: sale => (
        <React.Fragment>
          <Button.Group basic size="mini">
            <Popup
              trigger={
                <Button icon="eye" onClick={() => this.props.onView(sale)} />
              }
              content="View details"
            />

            {sale.statusId === 4 && this.props.roleId === "3" && (
              <Popup
                trigger={
                  <Button
                    icon="dollar"
                    onClick={() => this.props.onInitPayment(sale)}
                  />
                }
                content="Initiate Payment"
              />
            )}

            {sale.statusId === 4 &&
              (this.props.roleId === "2" ||
                this.props.roleId === "4" ||
                this.props.roleId === "7") && (
                <Popup
                  trigger={
                    <Button
                      icon="calculator"
                      onClick={() => this.props.onCheckPayment(sale)}
                    />
                  }
                  content="Check Payment"
                />
              )}

            {sale.statusId === 1 && this.props.roleId === "3" && (
              <Popup
                trigger={
                  <Button
                    icon="trash"
                    onClick={() => this.props.onDelete(sale)}
                  />
                }
                content="Delete/Archive"
              />
            )}
            {/* {this.props.roleId === "2" && sale.statusId === 1 && (
              <Popup
                trigger={
                  <Button
                    icon="thumbs up"
                    onClick={() => this.props.onVerified(sale)}
                  />
                }
                content="Verified"
              />
            )} */}

            {(this.props.roleId === "4" || this.props.roleId === "2") &&
              (sale.statusId === 2 || sale.statusId === 1) && (
                <Popup
                  trigger={
                    <Button
                      icon="check"
                      onClick={() => this.props.onApprove(sale)}
                    />
                  }
                  content="Approve"
                />
              )}
            {(this.props.roleId === "4" ||
              this.props.roleId === "7" ||
              this.props.roleId === "2") &&
              sale.statusId < 4 &&
              sale.statusId >= 1 && (
                <Popup
                  trigger={
                    <Button
                      icon="cancel"
                      onClick={() => this.props.onCancel(sale)}
                    />
                  }
                  content="Reject"
                />
              )}
            {sale.statusId === 3 && this.props.roleId === "1" && (
              <Popup
                trigger={
                  <Button
                    icon="database"
                    onClick={() => this.props.onExecuted(sale)}
                  />
                }
                content="Executed"
              />
            )}
          </Button.Group>
        </React.Fragment>
      )
    }
  ];
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const { sales, onSort, onSearch, sortColumn, allSales } = this.props;

    return (
      <Table
        columns={this.columns}
        sortColumn={sortColumn}
        onSort={onSort}
        onSearch={onSearch}
        data={sales}
        allData={allSales}
      />
    );
  }
}

export default SalesTable;
