import React, { Component } from "react";
import Table from "../../../components/common/table";
import { Button, Popup } from "semantic-ui-react";

const getBadge = status => {
  if (status === null) {
    return "badge badge-warning";
  }
  return status === "approved"
    ? "badge badge-success"
    : status === "verified"
    ? "badge badge-secondary"
    : status === "Initiated"
    ? "badge badge-warning"
    : status === "executed"
    ? "badge badge-primary"
    : status === "rejected"
    ? "badge badge-danger"
    : "badge badge-warning";
};

const getStatusLabel = statusId => {
  return statusId === 1
    ? "initiated"
    : statusId === 2
    ? "verified"
    : statusId === 3
    ? "approved"
    : statusId === 4
    ? "executed"
    : statusId === 5
    ? "rejected"
    : "initiated";
};

class StockMvntsTable extends Component {
  columns = [
    { path: "initiatedtime", lable: "Date" },
    { path: "entryname", lable: "Entry Label" },
    { path: "quantity", lable: "Quantity" },
    { path: "initiatedbyName", lable: "Initiator" },
    { path: "stockInName", lable: "Stock In" },
    { path: "stockOutName", lable: "Stock Out" },
    { path: "productName", lable: "Product" },
    {
      key: "stat",
      content: mvt => (
        <span className={getBadge(mvt.statusLabel)}>
          {getStatusLabel(mvt.statusId)}
        </span>
      ),
      lable: "Status"
    },
    {
      key: "edit",
      lable: "Actions",
      content: mvt => (
        <React.Fragment>
          <Button.Group size="mini" basic>
            {/* <Popup
              trigger={
                <Button icon="edit" onClick={() => this.props.onEdit(mvt)} />
              }
              content="Edit"
            /> */}

            {mvt.statusId === 1 && this.props.roleId === "4" && (
              <Popup
                trigger={
                  <Button
                    icon="cancel"
                    onClick={() => this.props.onCancel(mvt)}
                  />
                }
                content="Reject"
              />
            )}

            {/* {this.props.roleId === "2" && mvt.statusId < 2 && (
              <Popup
                trigger={
                  <Button
                    icon="thumbs up"
                    onClick={() => this.props.onVerified(mvt)}
                  />
                }
                content="Verified"
              />
            )} */}

            {this.props.roleId === "4" && mvt.statusId === 1 && (
              <Popup
                trigger={
                  <Button
                    icon="check"
                    onClick={() => this.props.onApprove(mvt)}
                  />
                }
                content="Approve"
              />
            )}
            {mvt.statusId === 3 && this.props.roleId === "1" && (
              <Popup
                trigger={
                  <Button
                    icon="database"
                    onClick={() => this.props.onExecuted(mvt)}
                  />
                }
                content="Executed"
              />
            )}
            {this.props.roleId === "1" && mvt.statusId === 1 && (
              <Popup
                trigger={
                  <Button
                    icon="trash"
                    onClick={() => this.props.onDelete(mvt)}
                  />
                }
                content="Delete/Archive"
              />
            )}
          </Button.Group>
        </React.Fragment>
      )
    }
  ];
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const { mvts, onSort, onSearch, sortColumn, allMvts } = this.props;

    return (
      <Table
        columns={this.columns}
        sortColumn={sortColumn}
        onSort={onSort}
        onSearch={onSearch}
        data={mvts}
        allData={allMvts}
      />
    );
  }
}

export default StockMvntsTable;
