import React, { Component } from "react";
import "react-datepicker/dist/react-datepicker.css";
import _ from "lodash";
import axios from "axios";
import DatePicker from "react-datepicker";
import { Modal, Button } from "react-bootstrap";
import { Card, CardBody, CardHeader, Col, Row, Table } from "reactstrap";

import Pagination from "../../components/common/pagination";
import { paginate } from "./../../../utils/paginate";
import ProductsTable from "./productsTable";
import url from "./../../../services/globalVars";
import { getProductsFromService } from "../../../services/productsServices";

const $ = require("jquery");

class Products extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products: [],
      pageSize: 10,
      currentPage: 1,
      sortColumn: { path: "product_code", order: "asc" },
      search: false,
      filter: "",
      inputMode: false,
      brands: [],
      productTypes: [],
      startDate: new Date(),
      formData: {
        id: 100,
        productcode: "",
        description: "",
        unitId: "",
        unitsalesprice: "",
        unitpurchasecost: "",
        minQuantity: "",
        retired: false,
        maxQuantity: "",
        barCode: "",
        quantityBalance: "",
        producttypeId: "",
        brandId: 0,
        brand: {},
        producttype: {},
        expireDate: new Date()
      },
      delModalShow: false,
      delProduct: {}
    };

    this.handleToggle = this.handleToggle.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.toggleCheck = this.toggleCheck.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.handleConfirmDelete = this.handleConfirmDelete.bind(this);
  }

  componentDidMount() {
    console.log(url);
    // let products = getProducts();
    this.getProducts();

    getProductsFromService().then(d => {
      console.log("Produccccccccct", d.data.content);
    });
    // Get Brands
    this.getBrands();
    // Get Product Types
    this.getProductTypes();
    // console.log(this.state.productTypes);
  }

  async getProductTypes() {
    let response2 = await axios.get(url + "Producttypes");
    let d2 = response2.data.content;
    this.setState({ productTypes: d2 });
  }

  async getBrands() {
    let response = await axios.get(url + "Brands");
    let d = response.data.content;
    this.setState({ brands: d });
  }

  async getProducts() {
    // Get Products
    let prodResp = await axios.get(url + "Products");
    let products = prodResp.data.content;

    let p2 = products.map(p => {
      return {
        id: p.id,
        productcode: p.productcode,
        description: p.description,
        quantityBalance: p.quantityBalance,
        brand: p.brand.name,
        brandId: p.brand.id
      };
    });

    this.setState({ products: p2 });
  }

  handleToggle() {
    this.setState({ inputMode: !this.state.inputMode });
  }

  handlePageChange = page => {
    this.setState({ currentPage: page });
  };

  handleEdit = product => {
    console.log(product);
    const { brands, productTypes } = this.state;
    let p = { ...product };

    p.quantityBalance = 100;

    let brand = brands.filter(e => {
      return e.id === parseInt(p.brandId);
    });

    p.brand = brand[0];

    console.log(p);
    axios.put(url + "Products", p).then(() => {
      this.getProducts();
    });
  };

  handleDelete = product => {
    console.log(product);
    let p = { ...product };
    // deleteProduct(product);

    this.setState({ delModalShow: true, delProduct: p });

    // console.log(this.state.delProduct);
  };

  handleConfirmDelete() {
    let product = this.state.delProduct;

    axios.delete(url + "Products/" + product.id, {}).then(() => {
      let products = { ...this.state.products };
      products = _.filter(products, p => {
        return p.id !== product.id;
      });
      this.setState({ products });
      this.getProducts();
    });

    this.setState({ delModalShow: false });
  }

  handleClose() {
    this.setState({ delModalShow: false });
  }

  handleSort = sortColumn => {
    this.setState({ sortColumn });
  };

  handleSearch = item => {
    this.setState({ search: true, filter: item });
  };

  handleDateChange = date => {
    this.setState({
      startDate: date
    });

    let formData = { ...this.state.formData };
    formData["expireDate"] = date;
    this.setState({ formData });
  };

  handleChange = ({ currentTarget: input }) => {
    const formData = { ...this.state.formData };
    const { brands, productTypes } = this.state;
    if (input.name === "brandId") {
      let brand = brands.filter(e => {
        return e.id === parseInt(input.value);
      });

      formData["brand"] = brand[0];
    } else if (input.name === "producttypeId") {
      let producttype = productTypes.filter(t => {
        return t.id === parseInt(input.value);
      });
      formData["producttype"] = producttype[0];
    } else {
      formData[input.name] = input.value;
    }
    this.setState({ formData });
  };

  toggleCheck() {
    let formData = { ...this.state.formData };
    formData["retired"] = !this.state.formData.retired;
    this.setState({ formData });
  }

  async handleSubmit(e) {
    e.preventDefault();

    let id = parseInt(Math.random()) + 100;
    this.state.formData.id = id;

    console.log(this.state.formData);
    await axios.post(url + "Products", this.state.formData);

    this.setState({ inputMode: !this.state.inputMode });

    this.getProducts();
    this.getBrands();
    this.getProductTypes();

    let formData = {};
    this.setState({ formData });
  }

  render() {
    const {
      pageSize,
      currentPage,
      products: allProducts,
      sortColumn,
      search,
      filter,
      brands,
      productTypes
    } = this.state;

    var searched = [];

    if (search && filter !== "") {
      const data = allProducts;
      let data_array = Object.values(data);
      let searchString = filter;
      searched = data_array.filter(element => {
        for (var property in element) {
          if (element.hasOwnProperty(property)) {
            if (
              element[property]
                .toString()
                .toLowerCase()
                .includes(searchString.toLowerCase())
            ) {
              return true;
            }
          }
        }
        return null;
      });
    } else {
      searched = allProducts;
    }
    const sorted = _.orderBy(searched, [sortColumn.path], [sortColumn.order]);

    const count = searched.length;

    const products = paginate(sorted, currentPage, pageSize);

    return (
      <React.Fragment>
        <h1> Products </h1> <br />
        <button className="btn btn-primary btn-sm" onClick={this.handleToggle}>
          Add Product
        </button>
        <br />
        <br />
        <div className="card animated fadeIn">
          <div className="card-body">
            {!this.state.inputMode && (
              <div className="table-responsive">
                <ProductsTable
                  allProducts={sorted}
                  products={products}
                  onEdit={this.handleEdit}
                  onDelete={this.handleDelete}
                  onSort={this.handleSort}
                  onSearch={this.handleSearch}
                  sortColumn={sortColumn}
                />
                <Pagination
                  itemsCount={count}
                  pageSize={pageSize}
                  currentPage={currentPage}
                  onPageChange={this.handlePageChange}
                />
              </div>
            )}
            {this.state.inputMode && (
              <form onSubmit={this.handleSubmit}>
                <div className="row animated fadeIn">
                  <div className="col-6">
                    <div className="form-group">
                      <label>Product Code</label>
                      <input
                        type="text"
                        className="form-control"
                        name="productcode"
                        value={this.state.formData.productcode}
                        onChange={this.handleChange}
                        placeholder="ACN1112"
                      />
                      {/* <small id="emailHelp" className="form-text text-muted">
                    We'll never share your email with anyone else.
                  </small> */}
                    </div>
                    <div className="form-group">
                      <div className="form-group">
                        <label>Product Description</label>
                        <textarea
                          className="form-control"
                          name="description"
                          value={this.state.formData.description}
                          onChange={this.handleChange}
                          rows="3"
                        />
                      </div>
                    </div>

                    <div className="form-group">
                      <label>Product Type</label>
                      <select
                        className="form-control"
                        name="producttypeId"
                        value={this.state.formData.producttype.id}
                        onChange={this.handleChange}
                      >
                        <option value="">--Select--</option>
                        {productTypes.map(p => (
                          <option key={p.id} value={p.id}>
                            {p.description}
                          </option>
                        ))}
                      </select>
                    </div>
                    <div className="form-group">
                      <label>Unit</label>
                      <select
                        className="form-control"
                        name="unitId"
                        value={this.state.formData.unitId}
                        onChange={this.handleChange}
                      >
                        <option>1</option>
                        <option>2</option>
                        <option>3</option>
                        <option>4</option>
                        <option>5</option>
                      </select>
                    </div>
                    <div className="form-group">
                      <label>Unit Sales Price</label>
                      <input
                        type="text"
                        className="form-control"
                        name="unitsalesprice"
                        aria-describedby="unitsalesprice"
                        placeholder="100"
                        value={this.state.formData.unitsalesprice}
                        onChange={this.handleChange}
                      />
                    </div>
                    <div className="form-group">
                      <label>Unit Purchase Cost</label>
                      <input
                        type="text"
                        className="form-control"
                        name="unitpurchasecost"
                        aria-describedby="unitpurchasecost"
                        placeholder="100"
                        value={this.state.formData.unitpurchasecost}
                        onChange={this.handleChange}
                      />
                      <small
                        id="unitsalesprice"
                        className="form-text text-muted"
                      >
                        The prices are in RWF
                      </small>
                    </div>
                    <div className="form-group">
                      <label>Brand</label>
                      <select
                        className="form-control"
                        name="brandId"
                        value={this.state.formData.brand.id}
                        onChange={this.handleChange}
                      >
                        <option value="">--Select--</option>
                        {brands.map(b => (
                          <option key={b.id} value={parseInt(b.id)}>
                            {b.name}
                          </option>
                        ))}
                      </select>
                    </div>
                    <button className="btn btn-primary btn-sm">Submit</button>
                  </div>
                  <div className="col-6">
                    <div className="form-group">
                      <label>Min Quantity</label>
                      <input
                        type="text"
                        className="form-control"
                        name="minQuantity"
                        aria-describedby="minQuantity"
                        placeholder="100"
                        value={this.state.formData.minQuantity}
                        onChange={this.handleChange}
                      />
                    </div>

                    <div className="form-group">
                      <label>Max Quantity</label>
                      <input
                        type="text"
                        className="form-control"
                        name="maxQuantity"
                        aria-describedby="maxQuantity"
                        placeholder="100"
                        value={this.state.formData.maxQuantity}
                        onChange={this.handleChange}
                      />
                    </div>
                    <div className="form-group form-check">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="retired"
                        checked={this.state.formData.retired}
                        onChange={this.toggleCheck}
                      />
                      <label className="form-check-label">Retired ?</label>
                    </div>

                    <div className="form-group">
                      <label>Bar Code</label>
                      <input
                        type="text"
                        className="form-control"
                        name="barCode"
                        aria-describedby="barCode"
                        placeholder="100"
                        value={this.state.formData.barCode}
                        onChange={this.handleChange}
                      />
                    </div>

                    <div className="form-group">
                      <label>Expiry Date: </label>
                      <br />
                      <DatePicker
                        className="form-control"
                        name="expireDate"
                        selected={this.state.startDate}
                        onChange={this.handleDateChange}
                      />
                    </div>

                    <div className="form-group">
                      <label>Quantity Balance</label>
                      <input
                        type="text"
                        className="form-control"
                        name="quantityBalance"
                        aria-describedby="quantityBalance"
                        placeholder="100"
                        value={this.state.formData.quantityBalance}
                        onChange={this.handleChange}
                      />
                    </div>
                  </div>
                </div>
              </form>
            )}
          </div>
        </div>
        <Modal show={this.state.delModalShow} onHide={this.handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>Archive Product</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            You are about to archive this product. Are you sure?
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={this.handleClose}>
              No, Cancel
            </Button>
            <Button variant="danger" onClick={this.handleConfirmDelete}>
              Yes, Delete
            </Button>
          </Modal.Footer>
        </Modal>
      </React.Fragment>
    );
  }
}

export default Products;
