import React, { Component } from "react";
import { paginate } from "./../../../utils/paginate";
import _ from "lodash";

import { Modal, Button } from "react-bootstrap";
import { Header, Icon, Image } from "semantic-ui-react";

import Pagination from "../../components/common/pagination";
import axios from "axios";
import url from "../../../services/globalVars";

import ClientsTable from "./clientsTable";

const style_width = {
  width: "400px",
  margin: "300px"
};

class Clients extends Component {
  constructor(props) {
    super(props);
    this.state = {
      openModal: false,
      clients: [],
      sortColumn: {
        path: "",
        lable: ""
      },
      currentPage: 1,
      pageSize: 10,
      search: false,
      filter: "",
      inputMode: false,
      formData: {
        createdAt: new Date(),
        firstName: "",
        lastName: "",
        id: null,
        names: "",
        contact: "",
        phonenumber: "",
        address: "",
        accountbalance: 0,
        retired: null,
        creditMax: 0,
        clientstatus: {
          id: 1,
          status: "active"
        },
        clientStatusId: 1,
        updated_at: new Date()
      }
    };

    this.handleToggle = this.handleToggle.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleCloseModal = this.handleCloseModal.bind(this);
    this.handleConfirmDelete = this.handleConfirmDelete.bind(this);
  }

  componentDidMount() {
    //TODO get all the clients from the backend
    this.getClients();
  }

  async getClients() {
    let response2 = await axios.get(url + "Clients");
    let d2 = response2.data.content;

    let clients = d2.map(c => {
      return {
        createdAt: c.createdAt,
        firstName: c.firstName,
        lastName: c.lastName,
        id: c.id,
        names: c.firstName + " " + c.lastName,
        contact: c.contact,
        phonenumber: c.phonenumber,
        address: c.address,
        accountbalance: c.accountbalance,
        retired: c.retired,
        creditMax: c.creditMax,
        clientstatus: c.clientstatus,
        updated_at: "2019-04-01"
      };
    });
    this.setState({
      clients
    });
  }

  handlePageChange = page => {
    this.setState({
      currentPage: page
    });
  };

  handleChange = ({ currentTarget: input }) => {
    const formData = {
      ...this.state.formData
    };
    formData[input.name] = input.value;
    this.setState({
      formData
    });
    console.log(this.state.formData);
  };

  handleDelete = client => {
    this.setState({
      openModal: true,
      cleintToDelete: client
    });
  };

  //TODO send put request to the backend
  handleEdit = client => {
    console.log(client);

    let p = {
      ...client
    };

    p.Location = "New Location";
    console.log(p);
  };

  handleSort = sortColumn => {
    this.setState({
      sortColumn
    });
  };
  handleSearch = item => {
    this.setState({
      search: true,
      filter: item
    });
  };
  handleToggle = () => {
    this.setState({
      inputMode: !this.state.inputMode
    });
  };

  handleCloseModal() {
    this.setState({
      openModal: false
    });
  }

  handleConfirmDelete() {
    let clients = {
      ...this.state.clients
    };

    axios
      .delete(url + "Clients/" + this.state.cleintToDelete.id, {})
      .then(() => {
        clients = _.filter(clients, p => {
          return p.id !== this.state.cleintToDelete.id;
        });
        this.setState({
          clients
        });
        this.getClients();
      });

    this.setState({
      openModal: false
    });
  }

  async handleSubmit(e) {
    e.preventDefault();
    console.log(this.state.formData);
    await axios.post(url + "Suppliers", this.state.formData);

    this.getSuppliers();
    this.handleToggle();
  }

  async handleSubmit(e) {
    e.preventDefault();
    console.log(this.state.formData);
    await axios.post(url + "Clients", this.state.formData);

    this.getClients();
    this.handleToggle();
  }
  render() {
    const {
      clients: allClients,
      sortColumn,
      pageSize,
      currentPage,
      search,
      filter,
      openModal
    } = this.state;

    let searched = [];

    if (search && filter !== "") {
      const data = allClients;
      let data_array = Object.values(data);
      let searchString = filter;
      searched = data_array.filter(element => {
        for (var property in element) {
          if (element.hasOwnProperty(property)) {
            if (
              element[property]
                .toString()
                .toLowerCase()
                .includes(searchString.toLowerCase())
            ) {
              return true;
            }
          }
        }
        return null;
      });
    } else {
      searched = allClients;
    }

    const sorted = _.orderBy(searched, [sortColumn.path], [sortColumn.order]);

    const count = searched.length;

    const clients = paginate(sorted, currentPage, pageSize);

    return (
      <React.Fragment>
        <Modal show={openModal} onHide={this.handleCloseModal}>
          <Modal.Header closeButton>
            <Modal.Title>Archive Client</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            You are about to archive this client. Are you sure?
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={this.handleCloseModal}>
              No, cancel
            </Button>
            <Button variant="danger" onClick={this.handleConfirmDelete}>
              Yes, i'm sure
            </Button>
          </Modal.Footer>
        </Modal>
        <h1> Clients </h1> <br />
        <button className="btn btn-primary btn-sm" onClick={this.handleToggle}>
          Add Client
        </button>
        <br />
        <br />
        <div className="card animated fadeIn">
          <div className="card-body">
            {!this.state.inputMode && (
              <div className="table-responsive">
                <ClientsTable
                  allClients={sorted}
                  clients={clients}
                  onEdit={this.handleEdit}
                  onDelete={this.handleDelete}
                  onSort={this.handleSort}
                  onSearch={this.handleSearch}
                  sortColumn={sortColumn}
                />
                <Pagination
                  itemsCount={count}
                  pageSize={pageSize}
                  currentPage={currentPage}
                  onPageChange={this.handlePageChange}
                />
              </div>
            )}
            {this.state.inputMode && (
              <form onSubmit={this.handleSubmit}>
                <div className="row animated fadeIn">
                  <div className="col-6">
                    <div className="form-group">
                      <label> First Name </label>
                      <input
                        type="text"
                        className="form-control"
                        name="firstName"
                        value={this.state.formData.firstName}
                        onChange={this.handleChange}
                        placeholder="First Name"
                      />
                    </div>
                    <div className="form-group">
                      <label> Last Name </label>
                      <input
                        type="text"
                        className="form-control"
                        name="lastName"
                        value={this.state.formData.lastName}
                        onChange={this.handleChange}
                        placeholder="Last Name"
                      />
                    </div>
                    <div className="form-group">
                      <label> Contact </label>
                      <input
                        type="text"
                        className="form-control"
                        name="contact"
                        value={this.state.formData.contact}
                        onChange={this.handleChange}
                        placeholder="Client Location"
                      />
                    </div>
                    <div className="form-group">
                      <label> Phone Number </label>
                      <input
                        type="text"
                        className="form-control"
                        name="phonenumber"
                        value={this.state.formData.phonenumber}
                        onChange={this.handleChange}
                        placeholder="07888.."
                      />
                    </div>
                    <div className="form-group">
                      <button className="btn btn-primary btn-sm">Submit</button>
                    </div>
                  </div>

                  <div className="col-6">
                    <div className="form-group">
                      <label> Address </label>
                      <input
                        type="text"
                        className="form-control"
                        name="address"
                        value={this.state.formData.address}
                        onChange={this.handleChange}
                        placeholder="Address"
                      />
                    </div>
                    <div className="form-group">
                      <label> Account Balance </label>
                      <input
                        type="text"
                        className="form-control"
                        name="accountbalance"
                        value={this.state.formData.accountbalance}
                        onChange={this.handleChange}
                        placeholder="0"
                      />
                    </div>
                    <div className="form-group">
                      <label> Maximum Credit </label>
                      <input
                        type="text"
                        className="form-control"
                        name="creditMax"
                        value={this.state.formData.creditMax}
                        onChange={this.handleChange}
                        placeholder="0"
                      />
                    </div>
                  </div>
                </div>
              </form>
            )}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Clients;
