import React, { Component } from "react";
import Table from "../../components/common/table";

class SuppliersTable extends Component {
  columns = [
    { path: "names", lable: "Names" },
    { path: "contact", lable: "Contact" },
    { path: "phonenumber", lable: "Phone Number" },
    { path: "address", lable: "Address" },
    { path: "accountbalance", lable: "Account Balance" },
    {
      key: "edit",
      lable: "Actions",
      content: supplier => (
        <React.Fragment>
          <div className="row">
            <button
              className="btn btn-sm btn-primary mr-2 ml-3"
              onClick={() => this.props.onEdit(supplier)}
            >
              <i className="cui-pencil icons font-lg" />
            </button>

            <button
              className="btn btn-sm btn-warning mr-2"
              onClick={() => this.props.onView(supplier)}
            >
              <i className="cui-list icons font-lg " />
            </button>

            <button
              className="btn btn-sm btn-danger"
              onClick={() => this.props.onDelete(supplier)}
            >
              <i className="cui-trash icons font-lg " />
            </button>
          </div>
        </React.Fragment>
      )
    }
  ];
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const {
      suppliers,
      onSort,
      onSearch,
      sortColumn,
      allSuppliers
    } = this.props;

    return (
      <Table
        columns={this.columns}
        sortColumn={sortColumn}
        onSort={onSort}
        onSearch={onSearch}
        data={suppliers}
        allData={allSuppliers}
      />
    );
  }
}

export default SuppliersTable;
