import React, { Component } from "react";
import PurchasesTable from "./purchasesTable";
import PurchaseDetailsRow from "./purchaseDetailsRow";
import { paginate } from "./../../../utils/paginate";
import _ from "lodash";
import Pagination from "../../components/common/pagination";
import axios from "axios";
import {
  ToastsContainer,
  ToastsStore,
  ToastsContainerPosition
} from "react-toasts";
import url, { getRoleId } from "../../../services/globalVars";
import { Accordion, Icon } from "semantic-ui-react";
import { timingSafeEqual } from "crypto";
import DetailsModal from "../../components/common/detailsModal";

import { Modal, Button } from "react-bootstrap";
import { Card, CardBody, CardHeader, Col, Row, Table } from "reactstrap";
import { getRoleIdFromDB } from "../../../services/userService";

class Purchases extends Component {
  constructor(props) {
    super(props);
    this.state = {
      numRows: 1,
      purchases: [],
      products: [],
      suppliers: [],
      stocks: [],
      tempPurchase: {
        productId: null,
        quantity: 0,
        unitPrice: 0,
        stockId: null
      },
      sortColumn: {
        path: "",
        lable: ""
      },
      details: {},
      detailsIndiv: {},
      currentPage: 1,
      pageSize: 10,
      search: false,
      filter: "",
      inputMode: false,
      formData: {
        selectedProducts: [],
        createdAt: new Date(),
        // initiatedby: 1,
        initiatedtime: this.getTime(),
        // verifiedby: {},
        verifiedTime: null,
        status: {},
        statusId: 1,
        supplier: {},
        supplierId: "",
        // approuved: false,
        updated_at: new Date()
      },
      selectedProducts: [],
      purchaseDetails: [],
      productsPurchased: [],
      temp_product: {},
      showDetails: false,
      showCommentBox: false,
      detailModal: false,
      quantity_update: "",
      purchase_id_update: "",
      activeIndex: -1
    };

    this.handleToggle = this.handleToggle.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.getTime = this.getTime.bind(this);
    this.handleCancel = this.handleCancel.bind(this);
    this.handleVerified = this.handleVerified.bind(this);
    this.handleApprove = this.handleApprove.bind(this);
    this.handleCloseDetailsModal = this.handleCloseDetailsModal.bind(this);
    this.handleChangeQuantity = this.handleChangeQuantity.bind(this);
    this.updateQuantity = this.updateQuantity.bind(this);
    this.getPurchaseDetailsIndiv = this.getPurchaseDetailsIndiv.bind(this);
    this.handleCloseCommentBox = this.handleCloseCommentBox.bind(this);
    this.handleCollapse = this.handleCollapse.bind(this);
    this.handleExecuted = this.handleExecuted.bind(this);
  }

  getTime() {
    let today = new Date();
    return today.getTime();
  }

  componentDidMount() {
    let username = localStorage.getItem("username");
    getRoleIdFromDB(username);

    //TODO get all the purchases from the backend
    this.getPurchases();
    this.getProducts();
    this.getStatus(1);
    this.getStocks();
    this.getSupplier(1);
    this.getSuppliers();
  }

  async getProducts() {
    // Get Products
    let prodResp = await axios.get(url + "Products");
    let products = prodResp.data.content;
    // let temp_p = [
    //   { key: 1, text: "product1", value: 1 },
    //   { key: 2, text: "product2", value: 2 },
    //   { key: 3, text: "product3", value: 3 }
    // ];

    let p2 = products.map(p => {
      return {
        key: p.id,
        text: p.description,
        value: p.id
      };
    });

    this.setState({
      products: p2
    });
  }

  async getProduct(id) {
    let res = await axios.get(url + "Products/" + id);
    let temp_product = res.data;

    this.setState({
      temp_product
    });
    return res.data;
  }

  async getPurchaseDetails(id) {
    let res = await axios.get(url + "Purchasedetails/" + id + "/purchase");
    let details = res.data.content;
    this.setState({
      details
    });
  }

  async getPurchaseDetailsIndiv(id) {
    await axios.get(url + "Purchasedetails/" + id).then(d => {
      let detailsIndiv = d.data;
      this.setState({
        detailsIndiv
      });
    });
  }

  async getStatus(id) {
    let res = await axios.get(url + "Status/" + id);
    let status = res.data;

    let formData = {
      ...this.state.formData
    };
    formData.status = status;
    this.setState({
      formData
    });
  }

  async getSuppliers() {
    let res = await axios.get(url + "Suppliers");
    let suppliers = res.data.content;

    this.setState({
      suppliers
    });
  }

  async getStocks() {
    let res = await axios.get(url + "Stocks");
    let stocks = res.data.content;

    this.setState({
      stocks
    });
  }

  async getSupplier(id) {
    let res = await axios.get(url + "Suppliers/" + id);
    let supplier = res.data;

    let formData = {
      ...this.state.formData
    };
    formData.supplier = supplier;
    this.setState({
      formData
    });
  }

  async getPurchases() {
    let response2 = await axios.get(url + "Purchases");
    let d2 = response2.data.content;

    let p = [];

    d2.map(d => {
      let pchs = {
        id: d.id,
        createdAt: d.createdAt,
        initiatedby: d.initiatedby,
        initiatedtime: d.initiatedtime,
        verifiedby: d.verifiedby,
        verifiedTime: d.verifiedTime,
        approuvedby: d.approuvedby,
        approuvedTime: d.approuvedTime,
        executedby: d.executedby,
        executedTime: d.executedTime,
        statusLabel: d.status && d.status.status,
        status: d.status,
        statusId: d.status && d.status.id,
        retired: d.retired,
        supplierNames: d.supplier && d.supplier.names,
        supplier: d.supplier,
        supplierId: d.supplier && d.supplier.id,
        approuved: d.approved,
        updated_at: d.updated_at
      };

      p.push(pchs);
      this.setState({
        purchases: p
      });
    });
  }

  handlePageChange = page => {
    this.setState({
      currentPage: page
    });
  };

  handleChange = ({ currentTarget: input }) => {
    const formData = {
      ...this.state.formData
    };

    if (input.name === "supplierId") {
      this.getSupplier(input.value);
    }

    formData[input.name] = input.value;
    this.setState({
      formData
    });
    console.log(this.state.formData);
  };

  handleChange2 = ({ currentTarget: input }) => {
    let purc = {
      ...this.state.tempPurchase
    };
    if (input.id === "productId") {
      this.getProduct(input.value).then(d => {
        purc.product = d;
        purc[input.id] = input.value;
      });
    } else {
      purc[input.id] = input.value;
    }

    console.log(purc);
    this.setState({
      tempPurchase: purc
    });
  };

  handleDelete = purchase => {
    // console.log(purchase);
    // deletestore(purchase);
    let purchases = {
      ...this.state.purchases
    };
    purchases = _.filter(purchases, p => {
      return p.id !== purchase.id;
    });

    //TODO Send delete request to the backend
    axios
      .delete(url + "Purchases/" + purchase.id, { validateStatus: false })
      .then(d => {
        console.log(d);
        if (d.status === 200) {
          ToastsStore.success("Success! " + d.statusText);
          this.setState({
            purchases
          });
          this.getPurchases();
        } else {
          ToastsStore.error("Error! " + d.data.message);
        }
      });
  };

  handleView = purchase => {
    console.log(purchase);
    this.setState({
      showDetails: true
    });
    this.getPurchaseDetails(purchase.id).then(() => {
      console.log(this.state.details);
      let productsPurchased = this.state.details;
      this.setState({
        productsPurchased
      });
    });
  };

  handleCloseDetailsModal() {
    this.setState({
      showDetails: false
    });
  }

  //TODO send put request to the backend
  handleEdit = purchase => {
    console.log(purchase);

    let p = {
      ...purchase
    };

    p.Location = "New Location";
    console.log(p);
  };

  handleSort = sortColumn => {
    this.setState({
      sortColumn
    });
  };
  handleSearch = item => {
    this.setState({
      search: true,
      filter: item
    });
  };
  handleToggle = () => {
    this.setState({
      inputMode: !this.state.inputMode
    });
  };

  async handleSubmit(e) {
    e.preventDefault();
    this.handleAddChild();

    // console.log(this.state.formData);
    await axios
      .post(url + "Purchases", this.state.formData, { validateStatus: false })
      .then(d => {
        // console.log(d.data);
        //TODO Post purchase details
        // const { productId, quantity, unitPrice, stockId } = {
        //   ...this.state.tempPurchase
        // };

        // {
        //   productId && this.state.purchaseDetails.push(this.state.tempPurchase);
        // }

        // console.log(this.state.purchaseDetails);

        if (d.status === 200) {
          ToastsStore.success("Success! " + d.statusText);
          console.log(d);
          for (var i = 0; i < this.state.purchaseDetails.length; i++) {
            let pd = {
              purchase_id: d.data.id,
              purchase: d.data,
              product: this.state.purchaseDetails[i].product,
              quantity: this.state.purchaseDetails[i].quantity
              // unitprice: this.state.temp_product.unitpurchasecost,
              // supplier: this.state.formData.supplier
            };

            {
              if (pd.purchase_id) {
                axios
                  .post(url + "Purchasedetails", pd, { validateStatus: false })
                  .then(e => {
                    if (e.status === 200) {
                      let purchaseDetails = [];
                      let formData = {
                        ...this.state.formData
                      };

                      formData.supplierId = "";
                      this.setState({
                        purchaseDetails,
                        formData,
                        numRows: 1
                      });
                    } else {
                      ToastsStore.error("Error! " + e.data.message);
                    }
                  });
              }
            }
          }
          this.getPurchases();
          this.handleToggle();
        } else {
          ToastsStore.error("Error! " + d.data.message);
        }
      });
  }

  handleAddChild = () => {
    const { productId, quantity, unitPrice, stockId } = {
      ...this.state.tempPurchase
    };
    // console.log(this.state.tempPurchase);

    {
      if (productId && quantity) {
        this.setState({
          numRows: this.state.numRows + 1
        });
        this.state.purchaseDetails.push(this.state.tempPurchase);

        let tempPurchase = {};
        this.setState({
          tempPurchase
        });
      }
    }

    console.log(this.state.purchaseDetails);
  };

  handleRemoveChild = () => {
    console.log("Delete");
  };

  async handleCancel(purchase) {
    console.log(purchase);
    let p = {
      ...purchase
    };
    p.status = {
      id: 5,
      status: "cancelled"
    };

    this.setState({
      showCommentBox: true
    });

    await axios
      .post(url + "Purchases", p, { validateStatus: false })
      .then(d => {
        if (d.status === 200) {
          ToastsStore.success("Success! " + d.statusText);
          this.getPurchases();
        } else {
          ToastsStore.error("Error! " + d.data.message);
        }
      });
  }

  handleCloseCommentBox() {
    this.setState({
      showCommentBox: false
    });
  }

  async handleVerified(purchase) {
    console.log(purchase);
    let p = {
      ...purchase
    };
    p.status = {
      id: 2,
      status: "verified"
    };

    await axios
      .post(url + "Purchases", p, { validateStatus: false })
      .then(d => {
        if (d.status === 200) {
          ToastsStore.success("Success! " + d.statusText);
          this.getPurchases();
        } else {
          ToastsStore.error("Error! " + d.data.message);
        }
      });
  }

  async handleExecuted(purchase) {
    console.log(purchase);
    let p = {
      ...purchase
    };
    p.status = {
      id: 4,
      status: "executed"
    };

    await axios.put(url + "Purchases", p, { validateStatus: false }).then(d => {
      console.log(d);
      if (d.status === 200) {
        ToastsStore.success("Success! " + d.statusText);
        this.getPurchases();
      } else {
        ToastsStore.error("Error! " + d.data.message);
      }
    });
  }

  async handleApprove(purchase) {
    console.log(purchase);
    let p = {
      ...purchase
    };
    p.status = {
      id: 3,
      status: "approved"
    };

    await axios
      .post(url + "Purchases", p, { validateStatus: false })
      .then(d => {
        if (d.status === 200) {
          ToastsStore.success("Success! " + d.statusText);
          this.getPurchases();
        } else {
          ToastsStore.error("Error! " + d.data.message);
        }
      });
  }

  handleChangeQuantity = ({ currentTarget: input }) => {
    this.setState({
      quantity_update: input.value,
      purchase_id_update: input.id
    });
  };

  async updateQuantity({ currentTarget: input }) {
    if (input.id === "b" + this.state.purchase_id_update) {
      this.getPurchaseDetailsIndiv(this.state.purchase_id_update).then(() => {
        console.log(this.state.detailsIndiv);
        let d = { ...this.state.detailsIndiv };
        d.quantity = this.state.quantity_update;

        if (this.state.quantity_update === "0") {
          axios
            .delete(url + "Purchasedetails/" + this.state.purchase_id_update)
            .then(() => {
              this.setState({
                showDetails: false
              });
            });
        } else {
          axios.post(url + "Purchasedetails", d).then(() => {
            this.setState({
              showDetails: false
            });
          });
        }
      });
    }
  }

  handleCollapse = (e, titleProps) => {
    const { index } = titleProps;
    const { activeIndex } = this.state;
    const newIndex = activeIndex === index ? -1 : index;

    this.setState({ activeIndex: newIndex });
  };

  render() {
    const {
      purchases: allPurchases,
      sortColumn,
      pageSize,
      currentPage,
      search,
      filter,
      suppliers,
      products,
      stocks,
      showDetails,
      productsPurchased,
      showCommentBox,
      activeIndex
    } = this.state;

    let searched = [];

    if (search && filter !== "") {
      const data = allPurchases;
      let data_array = Object.values(data);
      let searchString = filter;
      searched = data_array.filter(element => {
        for (var property in element) {
          if (element.hasOwnProperty(property)) {
            if (
              element[property]
                .toString()
                .toLowerCase()
                .includes(searchString.toLowerCase())
            ) {
              return true;
            }
          }
        }
        return null;
      });
    } else {
      searched = allPurchases;
    }

    const sorted = _.orderBy(searched, [sortColumn.path], [sortColumn.order]);

    const count = searched.length;

    const purchases = paginate(sorted, currentPage, pageSize);

    const children = [];
    for (var i = 0; i < this.state.numRows; i++) {
      children.push(
        <PurchaseDetailsRow
          key={i}
          number={i}
          onRemove={this.handleRemoveChild}
          products={products}
          stocks={stocks}
          onChange={this.handleChange2}
        />
      );
    }

    const roleId = getRoleId();

    return (
      <React.Fragment>
        <ToastsContainer
          store={ToastsStore}
          position={ToastsContainerPosition.TOP_RIGHT}
          lightBackground
        />
        {/* <ToastsContainer
          store={ToastsStore}
          position={ToastContainer.POSITION.TOP_LEFT}
          lightBackground
        /> */}
        <Modal show={showDetails} onHide={this.handleCloseDetailsModal}>
          <Modal.Header closeButton>
            <Modal.Title> Purchase Details </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Accordion>
              {productsPurchased.map((p, i) => (
                <div key={p.id}>
                  <Accordion.Title
                    active={activeIndex === i}
                    index={i}
                    onClick={this.handleCollapse}
                  >
                    <Icon name="dropdown" />
                    {p.product.productcode}
                  </Accordion.Title>

                  <Accordion.Content active={activeIndex === i}>
                    <div key={p.id} className="card">
                      <div className="card-body">
                        <p className="card-text"> Quantity: {p.quantity} </p>
                        {(roleId === "4" ||
                          roleId === "5" ||
                          roleId === "7") && (
                          <div>
                            <p>
                              Unit Cost:{" "}
                              <strong>{p.product.unitpurchasecost}</strong> RWF
                            </p>
                            <div className="row">
                              <div className="col-6">
                                <div className="form-group">
                                  <label>Adjust the quantity</label>
                                  <input
                                    type="text"
                                    className="form-control"
                                    name="quantity_update"
                                    id={p.id}
                                    onChange={this.handleChangeQuantity}
                                  />
                                </div>

                                <div className="form-group">
                                  <button
                                    id={"b" + p.id}
                                    className="btn btn-sm btn-primary"
                                    onClick={this.updateQuantity}
                                  >
                                    Submit
                                  </button>
                                </div>
                              </div>
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                  </Accordion.Content>
                </div>
              ))}
            </Accordion>
          </Modal.Body>
        </Modal>
        <Modal show={showCommentBox} onHide={this.handleCloseCommentBox}>
          <Modal.Header closeButton>
            <Modal.Title>Please provide comments for the rejection</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div className="col-8">
              <div className="form-group">
                <textarea className="form-control" name="comments" />
              </div>

              <div className="form-group">
                <button
                  className="btn btn-sm btn-primary"
                  onClick={this.handleCloseCommentBox}
                >
                  Submit
                </button>
              </div>
            </div>
          </Modal.Body>

          {/* <Modal.Footer>
                    <Button variant="secondary" onClick={this.handleCloseDetailsModal}>
                      No, Cancel
                    </Button>
                    <Button variant="danger" onClick={this.handleConfirmDelete}>
                      Yes, Delete
                    </Button>
                  </Modal.Footer> */}
        </Modal>
        <h1> Purchases </h1> <br />
        {roleId === "1" && (
          <button
            className="btn btn-primary btn-sm"
            onClick={this.handleToggle}
          >
            Add Purchase
          </button>
        )}
        <br />
        <br />
        <div className="card animated fadeIn">
          <div className="card-body">
            {!this.state.inputMode && (
              <div className="table-responsive">
                <PurchasesTable
                  allPurchases={sorted}
                  purchases={purchases}
                  onEdit={this.handleEdit}
                  onDelete={this.handleDelete}
                  onView={this.handleView}
                  onSort={this.handleSort}
                  onSearch={this.handleSearch}
                  sortColumn={sortColumn}
                  onCancel={this.handleCancel}
                  onVerified={this.handleVerified}
                  onApprove={this.handleApprove}
                  onExecuted={this.handleExecuted}
                  roleId={roleId}
                />
                <Pagination
                  itemsCount={count}
                  pageSize={pageSize}
                  currentPage={currentPage}
                  onPageChange={this.handlePageChange}
                />
              </div>
            )}
            {this.state.inputMode && (
              <form>
                <div className="row animated fadeIn">
                  <div className="col-12">
                    <div className="col-4">
                      <div className="form-group">
                        <label> Supplier </label>
                        <select
                          className="form-control"
                          name="supplierId"
                          value={this.state.formData.supplierId}
                          onChange={this.handleChange}
                        >
                          <option value=""> --Select Supplier-- </option>
                          {suppliers.map(s => (
                            <option key={s.id} value={s.id}>
                              {s.names}
                            </option>
                          ))}
                        </select>
                      </div>
                    </div>
                    <div className="form-group">
                      <button
                        type="button"
                        className="btn btn-sm btn-warning"
                        onClick={this.handleAddChild}
                      >
                        +
                      </button>
                      <button
                        className="btn btn-sm btn-danger ml-2"
                        type="button"
                        onClick={this.handleRemoveChild}
                      >
                        x
                      </button>
                    </div>
                    {children}
                    <div className="form-group">
                      <button
                        className="btn btn-primary btn-sm"
                        onClick={this.handleSubmit}
                      >
                        Submit
                      </button>
                    </div>
                  </div>
                </div>
              </form>
            )}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Purchases;
