import React, { Component } from "react";
import { paginate } from "../../../../utils/paginate";
import _ from "lodash";
import Pagination from "../../../components/common/pagination";
import url, { getRoleId } from "./../../../../services/globalVars";
import StockMvntsTable from "./stockMvntsTable";
import axios from "axios";
import {
  ToastsContainer,
  ToastsStore,
  ToastsContainerPosition
} from "react-toasts";
import { Modal, Button } from "react-bootstrap";
import { Card, CardBody, CardHeader, Col, Row, Table } from "reactstrap";
import { getRoleIdFromDB } from "../../../../services/userService";

class StockMvnts extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mvtToDelete: {},
      openModal: false,
      openErrorModal: false,
      message: "",
      mvts: [],
      stocks: [],
      products: [],
      sortColumn: {
        path: "reference",
        lable: "reference"
      },
      currentPage: 1,
      pageSize: 10,
      search: false,
      filter: "",
      inputMode: false,
      formData: {
        initiatedby: {},
        initiatedbyId: "",
        initiatedtime: new Date(),
        entryname: "",
        quantity: "",
        stockIn: {},
        stockInId: "",
        stockOut: {},
        stockOutId: "",
        product: "",
        productId: "",
        status: "pending",
        verifiedby: null,
        verifiedTime: null,
        approuvedby: null,
        approuvedTime: null,
        executedby: null,
        executedTime: null,
        status: {}
      }
    };

    this.handleToggle = this.handleToggle.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleApprove = this.handleApprove.bind(this);
    this.handleExecuted = this.handleExecuted.bind(this);
    this.handleCancel = this.handleCancel.bind(this);
    this.handleVerified = this.handleVerified.bind(this);
    this.handleCloseModal = this.handleCloseModal.bind(this);
    this.handleCloseErrorModal = this.handleCloseErrorModal.bind(this);
    this.handleConfirmDelete = this.handleConfirmDelete.bind(this);
  }

  componentDidMount() {
    let username = localStorage.getItem("username");
    getRoleIdFromDB(username);

    //TODO get all the mvts from the backend
    this.getMvnts();

    this.getProducts();
    this.getStocks();
    this.getUser(1);
  }

  async getMvnts() {
    let response2 = await axios.get(url + "StockMvnts");
    let d2 = response2.data.content;
    let mvts = [];
    d2.map(d => {
      let mvt = {
        id: d.id,
        initiatedtime: d.initiatedtime,
        entryname: d.entryname,
        quantity: d.quantity,
        stockInName: d.stockIn.description,
        stockIn: d.stockIn,
        stockInId: d.stockIn.id,
        stockOut: d.stockOut,
        stockOutName: d.stockOut.description,
        stockOutId: d.stockOut.id,
        initiatedbyName: d.initiatedby.firstName,
        initiatedby: d.initiatedby,
        initiatedbyId: d.initiatedby.id,
        product: d.product,
        productName: d.product.description,
        productId: d.product.id,
        statusLabel: d.status.status,
        status: d.status,
        statusId: d.status.id
      };

      mvts.push(mvt);
    });

    this.setState({
      mvts
    });
  }

  async getStatus(id) {
    let res = await axios.get(url + "Status/" + id);
    let status = res.data;

    let formData = {
      ...this.state.formData
    };
    formData.status = status;
    this.setState({
      formData
    });
  }

  async getProducts() {
    let res = await axios.get(url + "Products");
    let products = res.data.content;

    this.setState({
      products
    });
  }

  async getStocks() {
    let res = await axios.get(url + "Stocks");
    let stocks = res.data.content;

    this.setState({
      stocks
    });
  }

  async getProduct(id) {
    let rp = await axios.get(url + "Products/" + id);
    let p = rp.data;

    let formData = {
      ...this.state.formData
    };

    formData.product = p;
    this.setState({
      formData
    });
  }

  async getStockIn(id) {
    let rp = await axios.get(url + "Stocks/" + id);
    let s = rp.data;

    let formData = {
      ...this.state.formData
    };

    formData.stockIn = s;
    this.setState({
      formData
    });
  }

  async getStockOut(id) {
    let rp = await axios.get(url + "Stocks/" + id);
    let s = rp.data;
    let formData = {
      ...this.state.formData
    };

    formData.stockOut = s;
    this.setState({
      formData
    });
  }

  async getUser(id) {
    let rp = await axios.get(url + "Users/" + id);
    let u = rp.data;
    let formData = {
      ...this.state.formData
    };

    formData.initiatedby = u;
    this.setState({
      formData
    });
  }

  handlePageChange = page => {
    this.setState({
      currentPage: page
    });
  };

  handleChange = ({ currentTarget: input }) => {
    const formData = {
      ...this.state.formData
    };
    formData[input.name] = input.value;
    this.setState({
      formData
    });

    if (input.name === "productId") {
      this.getProduct(input.value);
    }

    if (input.name === "stockInId") {
      this.getStockIn(input.value);
    }

    if (input.name === "stockOutId") {
      this.getStockOut(input.value);
    }

    console.log(this.state.formData);
  };

  // handleDelete = order => {
  //   // console.log(order);
  //   // deleteorder(order);
  //   let mvts = {
  //     ...this.state.mvts
  //   };
  //   mvts = _.filter(mvts, p => {
  //     return p.id !== order.id;
  //   });

  //   this.setState({
  //     mvts
  //   });

  //   //TODO Send delete request to the backend
  //   axios.delete(url + "StockMvnts/" + order.id, {}).then(() => {
  //     this.getMvnts();
  //   });
  // };

  //TODO send put request to the backend
  handleEdit = mvt => {
    console.log(mvt);

    let p = {
      ...mvt
    };
  };

  handleDelete = mvt => {
    this.setState({
      openModal: true,
      mvtToDelete: mvt
    });
  };

  handleConfirmDelete() {
    let mvts = {
      ...this.state.mvts
    };

    axios
      .delete(url + "StockMvnts/" + this.state.mvtToDelete.id, {
        validateStatus: false
      })
      .then(d => {
        if (d.status === 200) {
          ToastsStore.success("Success! " + d.statusText);
          mvts = _.filter(mvts, p => {
            return p.id !== this.state.mvtToDelete.id;
          });
          this.setState({
            mvts
          });
          this.getMvnts();
        } else {
          ToastsStore.error("Error! " + d.data.message);
        }
      });

    this.setState({
      openModal: false
    });
  }

  handleCloseModal() {
    this.setState({
      openModal: false
    });
  }

  handleCloseErrorModal() {
    this.setState({
      openErrorModal: false
    });
  }

  handleSort = sortColumn => {
    this.setState({
      sortColumn
    });
  };
  handleSearch = item => {
    this.setState({
      search: true,
      filter: item
    });
  };
  handleToggle = () => {
    this.setState({
      inputMode: !this.state.inputMode
    });
  };

  async handleCancel(stockMvt) {
    console.log(stockMvt);
    let p = {
      ...stockMvt
    };
    p.status = {
      id: 5,
      status: "cancelled"
    };

    await axios
      .post(url + "StockMvnts", p, { validateStatus: false })
      .then(d => {
        if (d.status === 200) {
          ToastsStore.success("Success! " + d.statusText);
          this.getMvnts();
        } else {
          ToastsStore.error("Error! " + d.data.message);
        }
      });
  }

  async handleVerified(stockMvt) {
    console.log(stockMvt);
    let p = {
      ...stockMvt
    };
    p.status = {
      id: 2,
      status: "verified"
    };

    await axios
      .post(url + "StockMvnts", p, { validateStatus: false })
      .then(d => {
        if (d.status === 200) {
          ToastsStore.success("Success! " + d.statusText);
          this.getMvnts();
        } else {
          ToastsStore.error("Error! " + d.data.message);
        }
      });
  }

  async handleExecuted(stockMvt) {
    console.log(stockMvt);
    let p = {
      ...stockMvt
    };
    p.status = {
      id: 4,
      status: "executed"
    };

    await axios
      .put(url + "StockMvnts", p, { validateStatus: false })
      .then(d => {
        console.log(d);
        if (d.status === 200) {
          ToastsStore.success("Success! " + d.statusText);
          this.getMvnts();
        } else {
          ToastsStore.error("Error! " + d.data.message);
        }
      });
  }

  async handleApprove(stockMvt) {
    console.log(stockMvt);
    let p = {
      ...stockMvt
    };
    p.status = {
      id: 3,
      status: "approved"
    };

    await axios
      .post(url + "StockMvnts", p, { validateStatus: false })
      .then(d => {
        if (d.status === 200) {
          ToastsStore.success("Success! " + d.statusText);
          this.getMvnts();
        } else {
          ToastsStore.error("Error! " + d.data.message);
        }
      });
  }

  handleSubmit(e) {
    e.preventDefault();
    let { productId, stockInId, stockOutId } = {
      ...this.state.formData
    };

    // this.getProduct(productId);
    // this.getStockIn(stockInId);
    // this.getStockOut(stockOutId);

    this.getStatus(1).then(() => {
      axios
        .post(url + "StockMvnts", this.state.formData, {
          validateStatus: false
        })
        .then(d => {
          if (d.status === 200) {
            ToastsStore.success("Success! " + d.statusText);
            this.handleToggle();
            this.getMvnts();
          } else {
            console.log(d.data.message);
            ToastsStore.error("Error! " + d.data.message);
          }
        });
    });
  }

  render() {
    const {
      mvts: allMvts,
      sortColumn,
      pageSize,
      currentPage,
      search,
      filter,
      products,
      stocks,
      openModal,
      openErrorModal,
      message
    } = this.state;

    let searched = [];

    if (search && filter !== "") {
      const data = allMvts;
      let data_array = Object.values(data);
      let searchString = filter;
      searched = data_array.filter(element => {
        for (var property in element) {
          if (element.hasOwnProperty(property)) {
            if (
              element[property]
                .toString()
                .toLowerCase()
                .includes(searchString.toLowerCase())
            ) {
              return true;
            }
          }
        }
        return null;
      });
    } else {
      searched = allMvts;
    }

    const sorted = _.orderBy(searched, [sortColumn.path], [sortColumn.order]);

    const count = searched.length;

    const mvts = paginate(sorted, currentPage, pageSize);

    const roleId = getRoleId();
    return (
      <React.Fragment>
        <ToastsContainer
          store={ToastsStore}
          position={ToastsContainerPosition.TOP_RIGHT}
          lightBackground
        />
        <Modal show={openModal} onHide={this.handleCloseModal}>
          <Modal.Header closeButton>
            <Modal.Title> Archive Stock Movement </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            You are about to archive this stock movement.Are you sure ?
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={this.handleCloseModal}>
              No, cancel
            </Button>
            <Button variant="danger" onClick={this.handleConfirmDelete}>
              Yes, i 'm sure
            </Button>
          </Modal.Footer>
        </Modal>
        <Modal show={openErrorModal} onHide={this.handleCloseErrorModal}>
          <Modal.Header closeButton>
            <Modal.Title> Error encounted!! </Modal.Title>
          </Modal.Header>
          <Modal.Body>{message}</Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={this.handleCloseErrorModal}>
              Try again
            </Button>
          </Modal.Footer>
        </Modal>
        <h1>Internal Stock Movements </h1> <br />
        {roleId === "1" && (
          <button
            className="btn btn-primary btn-sm"
            onClick={this.handleToggle}
          >
            New Stock Movement
          </button>
        )}
        <br />
        <br />
        <div className="card animated fadeIn">
          <div className="card-body">
            {!this.state.inputMode && (
              <div className="table-responsive">
                <StockMvntsTable
                  allMvts={sorted}
                  mvts={mvts}
                  onEdit={this.handleEdit}
                  onDelete={this.handleDelete}
                  onSort={this.handleSort}
                  onSearch={this.handleSearch}
                  sortColumn={sortColumn}
                  onCancel={this.handleCancel}
                  onVerified={this.handleVerified}
                  onApprove={this.handleApprove}
                  onApprove={this.handleApprove}
                  onExecuted={this.handleExecuted}
                  roleId={roleId}
                />
                <Pagination
                  itemsCount={count}
                  pageSize={pageSize}
                  currentPage={currentPage}
                  onPageChange={this.handlePageChange}
                />
              </div>
            )}
            {this.state.inputMode && (
              <form onSubmit={this.handleSubmit}>
                <div className="row animated fadeIn">
                  <div className="col-6">
                    <div className="form-group">
                      <label> Entry Label </label>
                      <input
                        type="text"
                        className="form-control"
                        name="entryname"
                        value={this.state.formData.entryname}
                        onChange={this.handleChange}
                        placeholder="Entry label"
                      />
                    </div>
                    <div className="form-group">
                      <label> Quantity </label>
                      <input
                        type="text"
                        className="form-control"
                        name="quantity"
                        value={this.state.formData.quantity}
                        onChange={this.handleChange}
                        placeholder="0"
                      />
                    </div>
                    <div className="form-group">
                      <label> Stock In </label>
                      <select
                        className="form-control"
                        name="stockInId"
                        value={this.state.formData.stockInId}
                        onChange={this.handleChange}
                      >
                        <option> --Select-- </option>
                        {stocks.map(s => (
                          <option key={s.id} value={s.id}>
                            {s.description}
                          </option>
                        ))}
                      </select>
                    </div>
                    <div className="form-group">
                      <label> Stock Out </label>
                      <select
                        className="form-control"
                        name="stockOutId"
                        value={this.state.formData.stockOutId}
                        onChange={this.handleChange}
                      >
                        <option> --Select-- </option>
                        {stocks.map(s => (
                          <option key={s.id} value={s.id}>
                            {s.description}
                          </option>
                        ))}
                      </select>
                    </div>
                    <div className="form-group">
                      <button className="btn btn-primary btn-sm">Submit</button>
                    </div>
                  </div>
                  <div className="col-6">
                    <div className="form-group">
                      <label> Product </label>
                      <select
                        className="form-control"
                        name="productId"
                        value={this.state.formData.productId}
                        onChange={this.handleChange}
                      >
                        <option> --Select-- </option>
                        {products.map(p => (
                          <option key={p.id} value={p.id}>
                            {p.productcode}
                          </option>
                        ))}
                      </select>
                    </div>
                  </div>
                </div>
              </form>
            )}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default StockMvnts;
