import React, { Component } from "react";
import Table from "../../components/common/table";

class ProductTypesTable extends Component {
  columns = [
    { path: "description", lable: "Description" },
    {
      key: "edit",
      lable: "Actions",
      content: productType => (
        <React.Fragment>
          <div className="row">
            <button
              className="btn btn-sm btn-primary mr-2 ml-3"
              onClick={() => this.props.onEdit(productType)}
            >
              <i className="cui-pencil icons font-lg" />
            </button>

            <button
              className="btn btn-sm btn-danger"
              onClick={() => this.props.onDelete(productType)}
            >
              <i className="cui-trash icons font-lg " />
            </button>
          </div>
        </React.Fragment>
      )
    }
  ];

  render() {
    const {
      productTypes,
      onSort,
      onSearch,
      sortColumn,
      allProductTypes
    } = this.props;
    return (
      <Table
        columns={this.columns}
        sortColumn={sortColumn}
        onSort={onSort}
        onSearch={onSearch}
        data={productTypes}
        allData={allProductTypes}
      />
    );
  }
}

export default ProductTypesTable;
