import React, { Component } from "react";
import StoresTable from "./storesTable";

import { Modal, Button } from "react-bootstrap";
import { Header, Icon, Image } from "semantic-ui-react";

import { paginate } from "./../../../utils/paginate";
import _ from "lodash";
import Pagination from "../../components/common/pagination";
import axios from "axios";
import url from "../../../services/globalVars";

const style_width = {
  width: "400px",
  margin: "300px"
};

class Stores extends Component {
  constructor(props) {
    super(props);
    this.state = {
      storeToDelete: {},
      stores: [],
      sortColumn: {
        path: "Description",
        lable: "Description"
      },
      currentPage: 1,
      pageSize: 10,
      search: false,
      filter: "",
      inputMode: false,
      formData: {
        description: "Stock en ville",
        location: "ville",
        contact: "Kigali",
        officephone: "07888888",
        retired: true,
        listOfSales: [],
        listOfPurchasedetails: [],
        listOfStockUser: [],
        listOfStockMvnt: []
      },
      openModal: false
    };

    this.handleToggle = this.handleToggle.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleCloseModal = this.handleCloseModal.bind(this);
    this.handleConfirmDelete = this.handleConfirmDelete.bind(this);
  }

  componentDidMount() {
    //TODO get all the stores from the backend
    this.getStores();
  }

  async getStores() {
    let response2 = await axios.get(url + "Stocks");
    let d2 = response2.data.content;
    this.setState({
      stores: d2
    });
  }

  handlePageChange = page => {
    this.setState({
      currentPage: page
    });
  };

  handleChange = ({ currentTarget: input }) => {
    const formData = {
      ...this.state.formData
    };
    formData[input.name] = input.value;
    this.setState({
      formData
    });
    console.log(this.state.formData);
  };

  handleDelete = store => {
    this.setState({
      openModal: true,
      storeToDelete: store
    });
  };

  handleCloseModal() {
    this.setState({
      openModal: false
    });
  }

  handleConfirmDelete() {
    let stores = {
      ...this.state.stores
    };

    axios.delete(url + "Stocks/" + this.state.storeToDelete.id, {}).then(() => {
      stores = _.filter(stores, p => {
        return p.id !== this.state.storeToDelete.id;
      });
      this.setState({
        stores
      });
      this.getStores();
    });

    this.setState({
      openModal: false
    });
  }

  //TODO send put request to the backend
  handleEdit = store => {
    console.log(store);

    let p = {
      ...store
    };

    p.Location = "New Location";
    console.log(p);
  };

  handleSort = sortColumn => {
    this.setState({
      sortColumn
    });
  };
  handleSearch = item => {
    this.setState({
      search: true,
      filter: item
    });
  };
  handleToggle = () => {
    this.setState({
      inputMode: !this.state.inputMode
    });
  };

  async handleSubmit(e) {
    e.preventDefault();
    console.log(this.state.formData);
    await axios.post(url + "Stocks", this.state.formData);

    this.getStores();
    this.handleToggle();
  }
  render() {
    const {
      stores: allStores,
      sortColumn,
      pageSize,
      currentPage,
      search,
      filter,
      openModal
    } = this.state;

    let searched = [];

    if (search && filter !== "") {
      const data = allStores;
      let data_array = Object.values(data);
      let searchString = filter;
      searched = data_array.filter(element => {
        for (var property in element) {
          if (element.hasOwnProperty(property)) {
            if (
              element[property]
                .toString()
                .toLowerCase()
                .includes(searchString.toLowerCase())
            ) {
              return true;
            }
          }
        }
        return null;
      });
    } else {
      searched = allStores;
    }

    const sorted = _.orderBy(searched, [sortColumn.path], [sortColumn.order]);

    const count = searched.length;

    const stores = paginate(sorted, currentPage, pageSize);

    return (
      <React.Fragment>
        <Modal show={openModal} onHide={this.handleCloseModal}>
          <Modal.Header closeButton>
            <Modal.Title>Archive Store</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            You are about to archive this store. Are you sure?
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={this.handleCloseModal}>
              No, cancel
            </Button>
            <Button variant="danger" onClick={this.handleConfirmDelete}>
              Yes, i'm sure
            </Button>
          </Modal.Footer>
        </Modal>
        <h1> Stores </h1> <br />
        <button className="btn btn-primary btn-sm" onClick={this.handleToggle}>
          Add Store{" "}
        </button>{" "}
        <br />
        <br />
        <div className="card animated fadeIn">
          <div className="card-body">
            {" "}
            {!this.state.inputMode && (
              <div className="table-responsive">
                <StoresTable
                  allStores={sorted}
                  stores={stores}
                  onEdit={this.handleEdit}
                  onDelete={this.handleDelete}
                  onSort={this.handleSort}
                  onSearch={this.handleSearch}
                  sortColumn={sortColumn}
                />{" "}
                <Pagination
                  itemsCount={count}
                  pageSize={pageSize}
                  currentPage={currentPage}
                  onPageChange={this.handlePageChange}
                />{" "}
              </div>
            )}{" "}
            {this.state.inputMode && (
              <form onSubmit={this.handleSubmit}>
                <div className="row animated fadeIn">
                  <div className="col-6">
                    <div className="form-group">
                      <label> Description </label>{" "}
                      <input
                        type="text"
                        className="form-control"
                        name="description"
                        value={this.state.formData.description}
                        onChange={this.handleChange}
                        placeholder="Store Description"
                      />
                    </div>
                    <div className="form-group">
                      <label> Location </label>{" "}
                      <input
                        type="text"
                        className="form-control"
                        name="location"
                        value={this.state.formData.location}
                        onChange={this.handleChange}
                        placeholder="Store Location"
                      />
                    </div>
                    <div className="form-group">
                      <label> Contact </label>{" "}
                      <input
                        type="text"
                        className="form-control"
                        name="contact"
                        value={this.state.formData.contact}
                        onChange={this.handleChange}
                        placeholder="Store Location"
                      />
                    </div>
                    <div className="form-group">
                      <label> Office Phone </label>{" "}
                      <input
                        type="text"
                        className="form-control"
                        name="officephone"
                        value={this.state.formData.officephone}
                        onChange={this.handleChange}
                        placeholder="Store Location"
                      />
                    </div>
                    <div className="form-group">
                      <button className="btn btn-primary btn-sm">
                        {" "}
                        Submit{" "}
                      </button>{" "}
                    </div>{" "}
                  </div>{" "}
                </div>{" "}
              </form>
            )}{" "}
          </div>{" "}
        </div>{" "}
      </React.Fragment>
    );
  }
}

export default Stores;
