import React, { Component } from "react";

import Pagination from "../../components/common/pagination";
import { paginate } from "../../../utils/paginate";

import ProductTypesTable from "./productTypesTable";
import _ from "lodash";
import axios from "axios";

import { Modal, Button } from "react-bootstrap";
import { Header, Icon, Image } from "semantic-ui-react";

import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import url from "./../../../services/globalVars";

const $ = require("jquery");

class Products extends Component {
  constructor(props) {
    super(props);
    this.state = {
      openModal: false,
      productTypeToDelete: {},
      productTypes: [],
      pageSize: 10,
      currentPage: 1,
      sortColumn: {
        path: "description",
        order: "asc"
      },
      search: false,
      filter: "",
      inputMode: false,
      productTypes: [],
      formData: {
        description: ""
      }
    };

    this.handleToggle = this.handleToggle.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.toggleCheck = this.toggleCheck.bind(this);
    this.handleCloseModal = this.handleCloseModal.bind(this);
    this.handleConfirmDelete = this.handleConfirmDelete.bind(this);
  }

  componentDidMount() {
    this.getProductTypes();
  }

  async getProductTypes() {
    let response2 = await axios.get(url + "Producttypes");
    let d2 = response2.data.content;
    this.setState({
      productTypes: d2
    });
  }

  handleToggle() {
    this.setState({
      inputMode: !this.state.inputMode
    });
  }

  handlePageChange = page => {
    this.setState({
      currentPage: page
    });
  };

  handleEdit = productType => {
    console.log(productType);

    //TODO update ProductType
  };

  handleDelete = productType => {
    this.setState({
      openModal: true,
      productTypeToDelete: productType
    });
  };

  handleConfirmDelete() {
    let productTypes = {
      ...this.state.productTypes
    };

    axios
      .delete(url + "Producttypes/" + this.state.productTypeToDelete.id, {})
      .then(() => {
        productTypes = _.filter(productTypes, p => {
          return p.id !== this.state.productTypeToDelete.id;
        });
        this.setState({
          productTypes
        });
        this.getProductTypes();
      });

    this.setState({
      openModal: false
    });
  }

  handleCloseModal() {
    this.setState({
      openModal: false
    });
  }

  handleSort = sortColumn => {
    this.setState({
      sortColumn
    });
  };

  handleSearch = item => {
    this.setState({
      search: true,
      filter: item
    });
  };

  handleDateChange = date => {
    this.setState({
      startDate: date
    });

    let formData = {
      ...this.state.formData
    };
    formData["expireDate"] = date;
    this.setState({
      formData
    });
  };

  handleChange = ({ currentTarget: input }) => {
    const formData = {
      ...this.state.formData
    };
    const { productTypes } = this.state;
    if (input.name === "brandId") {
      let brand = productTypes.filter(e => {
        return e.id === parseInt(input.value);
      });

      formData["brand"] = brand[0];
    } else if (input.name === "producttypeId") {
      let producttype = productTypes.filter(t => {
        return t.id === parseInt(input.value);
      });
      formData["producttype"] = producttype[0];
    } else {
      formData[input.name] = input.value;
    }
    this.setState({
      formData
    });
  };

  toggleCheck() {
    let formData = {
      ...this.state.formData
    };
    formData["retired"] = !this.state.formData.retired;
    this.setState({
      formData
    });
  }

  async handleSubmit(e) {
    e.preventDefault();

    // let id = parseInt(Math.random()) + 100;
    // this.state.formData.id = id;

    console.log(this.state.formData);
    await axios.post(url + "Producttypes", this.state.formData);

    this.setState({
      inputMode: !this.state.inputMode
    });

    this.getProductTypes();

    let formData = {};
    this.setState({
      formData
    });
  }

  render() {
    const {
      pageSize,
      currentPage,
      productTypes: allproductTypes,
      sortColumn,
      search,
      filter,
      openModal
    } = this.state;

    var searched = [];

    if (search && filter !== "") {
      const data = allproductTypes;
      let data_array = Object.values(data);
      let searchString = filter;
      searched = data_array.filter(element => {
        for (var property in element) {
          if (element.hasOwnProperty(property)) {
            if (
              element[property]
                .toString()
                .toLowerCase()
                .includes(searchString.toLowerCase())
            ) {
              return true;
            }
          }
        }
        return null;
      });
    } else {
      searched = allproductTypes;
    }
    const sorted = _.orderBy(searched, [sortColumn.path], [sortColumn.order]);

    const count = searched.length;

    const productTypes = paginate(sorted, currentPage, pageSize);

    return (
      <React.Fragment>
        <Modal show={openModal} onHide={this.handleCloseModal}>
          <Modal.Header closeButton>
            <Modal.Title> Archive Product Type </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            You are about to archive this product type.Are you sure ?
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={this.handleCloseModal}>
              No, cancel
            </Button>
            <Button variant="danger" onClick={this.handleConfirmDelete}>
              Yes, i 'm sure
            </Button>
          </Modal.Footer>
        </Modal>
        <h1> Products Types </h1> <br />
        <button className="btn btn-primary btn-sm" onClick={this.handleToggle}>
          Add Product
        </button>
        <br />
        <br />
        <div className="card animated fadeIn">
          <div className="card-body">
            {!this.state.inputMode && (
              <div className="table-responsive">
                <ProductTypesTable
                  allProductTypes={sorted}
                  productTypes={productTypes}
                  onEdit={this.handleEdit}
                  onDelete={this.handleDelete}
                  onSort={this.handleSort}
                  onSearch={this.handleSearch}
                  sortColumn={sortColumn}
                />
                <Pagination
                  itemsCount={count}
                  pageSize={pageSize}
                  currentPage={currentPage}
                  onPageChange={this.handlePageChange}
                />
              </div>
            )}
            {this.state.inputMode && (
              <form onSubmit={this.handleSubmit}>
                <div className="row animated fadeIn">
                  <div className="col-6">
                    <div className="form-group">
                      <label> Description </label>
                      <input
                        type="text"
                        className="form-control"
                        name="description"
                        value={this.state.formData.description}
                        onChange={this.handleChange}
                        placeholder="Store Description"
                      />
                    </div>
                    <div className="form-group">
                      <button className="btn btn-primary btn-sm">Submit</button>
                    </div>
                  </div>
                </div>
              </form>
            )}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Products;
