import React, { Component } from "react";

class PurchaseDetailsRow extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const { products, number, onChange, stocks, onRemove } = { ...this.props };
    return (
      <div className="row">
        <div className="col-3">
          <div className="form-group">
            <label>Product {number + 1}</label>
            <select
              className="form-control"
              name={"product" + (number + 1)}
              onChange={onChange}
              id="productId"
            >
              <option value>Select</option>
              {products.map(p => (
                <option value={p.key} key={p.key}>
                  {p.text}
                </option>
              ))}
            </select>
          </div>
        </div>

        <div className="col-3">
          <div className="form-group">
            <label>Quantity</label>
            <input
              type="text"
              className="form-control"
              name={"quantity" + (number + 1)}
              onChange={onChange}
              id="quantity"
            />
          </div>
        </div>
      </div>
    );
  }
}

export default PurchaseDetailsRow;
