import React, { Component } from "react";
import Table from "../../components/common/table";

const getBadge = status => {
  if (status === null) {
    return "badge badge-warning";
  }
  return status === "active"
    ? "badge badge-success"
    : status === "suspended"
    ? "badge badge-warning"
    : status === "inactive"
    ? "badge badge-danger"
    : "badge badge-success";
};

const getStatusLabel = statusId => {
  return statusId === 1
    ? "active"
    : statusId === 2
    ? "suspended"
    : statusId === 3
    ? "inactive"
    : "active";
};

class ClientsTable extends Component {
  columns = [
    { path: "names", lable: "First Name" },
    { path: "contact", lable: "Contact" },
    { path: "phonenumber", lable: "Phone Number" },
    { path: "address", lable: "Address" },
    { path: "accountbalance", lable: "Account Balance" },
    {
      key: "stat",
      content: client => (
        <span className={getBadge(client.clientstatus.status)}>
          {getStatusLabel(client.clientstatus.id)}
        </span>
      ),
      lable: "Status"
    },
    {
      key: "edit",
      lable: "Actions",
      content: client => (
        <React.Fragment>
          <div className="row">
            <button
              className="btn btn-sm btn-primary mr-2 ml-3"
              onClick={() => this.props.onEdit(client)}
            >
              <i className="cui-pencil icons font-lg" />
            </button>

            <button
              className="btn btn-sm btn-danger"
              onClick={() => this.props.onDelete(client)}
            >
              <i className="cui-trash icons font-lg " />
            </button>
          </div>
        </React.Fragment>
      )
    }
  ];
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const { clients, onSort, onSearch, sortColumn, allClients } = this.props;

    return (
      <Table
        columns={this.columns}
        sortColumn={sortColumn}
        onSort={onSort}
        onSearch={onSearch}
        data={clients}
        allData={allClients}
      />
    );
  }
}

export default ClientsTable;
