import React, { Component } from "react";
import Table from "../../components/common/table";

class ProductsTable extends Component {
  columns = [
    { path: "productcode", lable: "Product Code" },
    { path: "description", lable: "Description" },
    { path: "quantityBalance", lable: "Quantity Balance" },
    { path: "brand", lable: "Brand" },
    {
      key: "edit",
      lable: "Actions",
      content: product => (
        <React.Fragment>
          <div className="row">
            <button
              className="btn btn-sm btn-primary mr-2 ml-3 "
              onClick={() => this.props.onEdit(product)}
            >
              <i className="cui-pencil icons font-lg" />
            </button>

            <button
              className="btn btn-sm btn-danger"
              onClick={() => this.props.onDelete(product)}
            >
              <i className="cui-trash icons font-lg " />
            </button>
          </div>
        </React.Fragment>
      )
    }
  ];

  render() {
    const { products, onSort, onSearch, sortColumn, allProducts } = this.props;
    return (
      <Table
        columns={this.columns}
        sortColumn={sortColumn}
        onSort={onSort}
        onSearch={onSearch}
        data={products}
        allData={allProducts}
      />
    );
  }
}

export default ProductsTable;
