import React, { Component } from "react";
import Table from "../../components/common/table";
import { Button, Popup } from "semantic-ui-react";

const getBadge = status => {
  if (status === null) {
    return "badge badge-warning";
  }
  return status === "approved"
    ? "badge badge-success"
    : status === "verified"
    ? "badge badge-secondary"
    : status === "Initiated"
    ? "badge badge-warning"
    : status === "executed"
    ? "badge badge-primary"
    : status === "rejected"
    ? "badge badge-danger"
    : "badge badge-warning";
};

const getStatusLabel = statusId => {
  return statusId === 1
    ? "initiated"
    : statusId === 2
    ? "verified"
    : statusId === 3
    ? "approved"
    : statusId === 4
    ? "executed"
    : statusId === 5
    ? "rejected"
    : "initiated";
};

class PurchasesTable extends Component {
  columns = [
    { path: "createdAt", lable: "Purchase Date" },
    { path: "updated_at", lable: "Updated" },
    { path: "supplierNames", lable: "Supplier" },
    {
      key: "stat",
      content: purchase => (
        <span className={getBadge(purchase.statusLabel)}>
          {getStatusLabel(purchase.statusId)}
        </span>
      ),
      lable: "Status"
    },
    {
      key: "edit",
      lable: "Actions",
      content: purchase => (
        <React.Fragment>
          <Button.Group basic size="mini">
            <Popup
              trigger={
                <Button
                  icon="eye"
                  onClick={() => this.props.onView(purchase)}
                />
              }
              content="View details"
            />

            {/* {purchase.statusId === 1 && this.props.roleId !== "1" && (
              <Popup
                trigger={
                  <Button
                    icon="edit"
                    onClick={() => this.props.onEdit(purchase)}
                  />
                }
                content="Edit"
              />
            )} */}

            {purchase.statusId === 1 && this.props.roleId === "1" && (
              <Popup
                trigger={
                  <Button
                    icon="trash"
                    onClick={() => this.props.onDelete(purchase)}
                  />
                }
                content="Delete/Archive"
              />
            )}
            {this.props.roleId === "4" && purchase.statusId === 1 && (
              <Popup
                trigger={
                  <Button
                    icon="thumbs up"
                    onClick={() => this.props.onVerified(purchase)}
                  />
                }
                content="Verified"
              />
            )}

            {this.props.roleId === "7" && purchase.statusId === 2 && (
              <Popup
                trigger={
                  <Button
                    icon="check"
                    onClick={() => this.props.onApprove(purchase)}
                  />
                }
                content="Approve"
              />
            )}
            {(this.props.roleId === "4" || this.props.roleId === "7") &&
              purchase.statusId !== 5 &&
              purchase.statusId >= 1 && (
                <Popup
                  trigger={
                    <Button
                      icon="cancel"
                      onClick={() => this.props.onCancel(purchase)}
                    />
                  }
                  content="Reject"
                />
              )}
            {purchase.statusId === 3 && this.props.roleId === "1" && (
              <Popup
                trigger={
                  <Button
                    icon="database"
                    onClick={() => this.props.onExecuted(purchase)}
                  />
                }
                content="Executed"
              />
            )}
          </Button.Group>
        </React.Fragment>
      )
    }
  ];
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const {
      purchases,
      onSort,
      onSearch,
      sortColumn,
      allPurchases
    } = this.props;

    return (
      <Table
        columns={this.columns}
        sortColumn={sortColumn}
        onSort={onSort}
        onSearch={onSearch}
        data={purchases}
        allData={allPurchases}
      />
    );
  }
}

export default PurchasesTable;
