import React, { Component, Suspense } from "react";
import { Redirect, Route, Switch } from "react-router-dom";
import { Container } from "reactstrap";

import {
  AppAside,
  AppBreadcrumb,
  AppFooter,
  AppHeader,
  AppSidebar,
  AppSidebarFooter,
  AppSidebarForm,
  AppSidebarHeader,
  AppSidebarMinimizer,
  AppSidebarNav
} from "@coreui/react";
// sidebar nav config
import navigation from "../../_nav";
import navigation1 from "../../_nav1";
import navigation2 from "../../_nav2";
import navigation3 from "../../_nav3";
// routes config
import routes from "../../routes";
import { getCurrentUser, getRoleIdFromDB } from "../../services/userService";
import { getRoleId } from "../../services/globalVars";

const DefaultAside = React.lazy(() => import("./DefaultAside"));
// const DefaultFooter = React.lazy(() => import("./DefaultFooter"));
const DefaultHeader = React.lazy(() => import("./DefaultHeader"));

class DefaultLayout extends Component {
  loading = () => (
    <div className="animated fadeIn pt-1 text-center"> Loading... </div>
  );

  constructor(props) {
    super(props);
    this.state = {
      roleId: null,
      user: {}
    };
  }

  componentDidMount() {
    let username = localStorage.getItem("username");
    getRoleIdFromDB(username).then(r => {
      if (r.data) {
        localStorage.setItem("roleId", r.data.role.id);

        const user = getCurrentUser();
        let d = getRoleId();
        this.setState({ roleId: d });
        console.log("Roleeeeee", this.state.roleId);

        this.setState({ user });
      } else {
        localStorage.setItem("roleId", "100");

        const user = getCurrentUser();
        let d = getRoleId();
        this.setState({ roleId: d });
        console.log("Roleeeeee", this.state.roleId);

        this.setState({ user });
      }
    });
  }

  signOut(e) {
    e.preventDefault();
    this.props.history.push("/login");
    localStorage.removeItem("user");
    localStorage.removeItem("roleId");
    localStorage.removeItem("imageUrl");
    localStorage.removeItem("username");
  }

  render() {
    const { user, roleId } = { ...this.state };
    return (
      <div className="app">
        <AppHeader fixed>
          <Suspense fallback={this.loading()}>
            <DefaultHeader onLogout={e => this.signOut(e)} />
          </Suspense>
        </AppHeader>
        <div className="app-body">
          <AppSidebar fixed display="lg">
            <AppSidebarHeader />
            <AppSidebarForm />
            <Suspense>
              <AppSidebarNav
                navConfig={
                  roleId === "4" ||
                  roleId === "5" ||
                  roleId === "7" ||
                  roleId === "2"
                    ? navigation
                    : roleId === "1" || roleId === "2"
                    ? navigation1
                    : roleId === "3"
                    ? navigation2
                    : navigation3
                }
                {...this.props}
              />
            </Suspense>
            <AppSidebarFooter />
            <AppSidebarMinimizer />
          </AppSidebar>
          <main className="main">
            <AppBreadcrumb appRoutes={routes} />
            <Container fluid>
              <Suspense fallback={this.loading()}>
                <Switch>
                  {routes.map((route, idx) => {
                    return route.component ? (
                      <Route
                        key={idx}
                        path={route.path}
                        exact={route.exact}
                        name={route.name}
                        render={props => {
                          if (!user) return <Redirect to="/login" />;
                          return <route.component {...props} />;
                        }}
                      />
                    ) : null;
                  })}
                  <Redirect from="/" to="/dashboard" />
                </Switch>
              </Suspense>
            </Container>
          </main>
          <AppAside fixed>
            <Suspense fallback={this.loading()}>
              <DefaultAside />
            </Suspense>
          </AppAside>
        </div>
        <AppFooter>
          <Suspense fallback={this.loading()}>
            {/* <DefaultFooter /> */}
          </Suspense>
        </AppFooter>
      </div>
    );
  }
}

export default DefaultLayout;
