import React, { Component } from "react";
import { Modal, Button } from "react-bootstrap";
import _ from "lodash";
import axios from "axios";
import { Header, Icon, Image } from "semantic-ui-react";

import { paginate } from "./../../../utils/paginate";
import Pagination from "../../components/common/pagination";
import url from "../../../services/globalVars";
import SuppliersTable from "./suppliersTable";
import SupplierOrdersTable from "./supplierOrdersTable";

const style_width = {
  width: "400px",
  margin: "300px"
};

class Suppliers extends Component {
  constructor(props) {
    super(props);
    this.state = {
      openModal: false,
      openModalDetails: false,
      suppliers: [],
      purchases: [],
      sortColumn: {
        path: "",
        lable: ""
      },
      sortColumnPurchase: {
        path: "",
        lable: ""
      },
      currentPage: 1,
      currentPagePurchases: 1,
      pageSize: 10,
      pageSizePurchase: 3,
      search: false,
      searchPurchase: false,
      filterPurchase: "",
      inputMode: false,
      formData: {
        names: "",
        contact: "",
        phonenumber: "",
        address: "",
        accountbalance: 0
      }
    };

    this.handleToggle = this.handleToggle.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleCloseModal = this.handleCloseModal.bind(this);
    this.handleConfirmDelete = this.handleConfirmDelete.bind(this);
    this.handleView = this.handleView.bind(this);
    this.handleCloseDetails = this.handleCloseDetails.bind(this);
  }

  componentDidMount() {
    //TODO get all the suppliers from the backend
    this.getSuppliers();
  }

  async getSuppliers() {
    let response2 = await axios.get(url + "Suppliers");
    let d2 = response2.data.content;
    this.setState({
      suppliers: d2
    });
  }

  async getOrdersBySupplier(id) {
    return await axios.get(url + "Purchases/" + id + "/supplier");
  }

  handlePageChange = page => {
    this.setState({
      currentPage: page
    });
  };

  handlePageChangePurchases = page => {
    this.setState({
      currentPagePurchases: page
    });
  };

  handleChange = ({ currentTarget: input }) => {
    const formData = {
      ...this.state.formData
    };
    formData[input.name] = input.value;
    this.setState({
      formData
    });
    console.log(this.state.formData);
  };

  handleDelete = supplier => {
    this.setState({
      openModal: true,
      supplierToDelete: supplier
    });
  };

  handleView = supplier => {
    this.setState({
      openModalDetails: true
    });
    this.getOrdersBySupplier(supplier.id).then(d => {
      let orders = d.data.content;
      let ods = [];
      for (var i = 0; i < orders.length; i++) {
        let d_new = {
          id: orders[i].id,
          status: orders[i].status.status,
          createdAt: orders[i].createdAt
        };
        ods.push(d_new);
      }

      this.setState({ purchases: ods });
    });
  };

  //TODO send put request to the backend
  handleEdit = supplier => {
    console.log(supplier);

    let p = {
      ...supplier
    };

    p.Location = "New Location";
    console.log(p);
  };

  handleSort = sortColumn => {
    this.setState({
      sortColumn
    });
  };

  handleSortPurchase = sortColumnPurchase => {
    this.setState({
      sortColumnPurchase
    });
  };

  handleSearch = item => {
    this.setState({
      search: true,
      filter: item
    });
  };
  handleSearchPurchases = item => {
    this.setState({
      searchPurchase: true,
      filterPurchase: item
    });
  };
  handleToggle = () => {
    this.setState({
      inputMode: !this.state.inputMode
    });
  };

  handleCloseModal() {
    this.setState({ openModal: false });
  }

  handleCloseDetails() {
    this.setState({ openModalDetails: false });
  }

  handleConfirmDelete() {
    let suppliers = {
      ...this.state.suppliers
    };

    axios
      .delete(url + "Suppliers/" + this.state.supplierToDelete.id, {})
      .then(() => {
        suppliers = _.filter(suppliers, p => {
          return p.id !== this.state.supplierToDelete.id;
        });
        this.setState({
          suppliers
        });
        this.getSuppliers();
      });

    this.setState({ openModal: false });
  }

  async handleSubmit(e) {
    e.preventDefault();
    console.log(this.state.formData);
    await axios.post(url + "Suppliers", this.state.formData);

    this.getSuppliers();
    this.handleToggle();
  }

  render() {
    const {
      suppliers: allSuppliers,
      sortColumn,
      sortColumnPurchase,
      pageSize,
      pageSizePurchase,
      currentPage,
      currentPagePurchases,
      search,
      searchPurchase,
      filter,
      filterPurchase,
      openModal,
      openModalDetails,
      purchases: allPurchases
    } = this.state;

    let searched = [];
    let searchedPurchase = [];

    if (search && filter !== "") {
      const data = allSuppliers;
      let data_array = Object.values(data);
      let searchString = filter;
      searched = data_array.filter(element => {
        for (var property in element) {
          if (element.hasOwnProperty(property)) {
            if (
              element[property]
                .toString()
                .toLowerCase()
                .includes(searchString.toLowerCase())
            ) {
              return true;
            }
          }
        }
        return null;
      });
    } else {
      searched = allSuppliers;
    }

    if (searchPurchase && filterPurchase !== "") {
      const data = allPurchases;
      let data_array = Object.values(data);
      let searchString = filterPurchase;
      searchedPurchase = data_array.filter(element => {
        for (var property in element) {
          if (element.hasOwnProperty(property)) {
            if (
              element[property]
                .toString()
                .toLowerCase()
                .includes(searchString.toLowerCase())
            ) {
              return true;
            }
          }
        }
        return null;
      });
    } else {
      searchedPurchase = allPurchases;
    }

    const sorted = _.orderBy(searched, [sortColumn.path], [sortColumn.order]);
    const sortedPurchase = _.orderBy(
      searchedPurchase,
      [sortColumnPurchase.path],
      [sortColumnPurchase.order]
    );

    const count = searched.length;

    const count_purchases = searchedPurchase.length;

    const suppliers = paginate(sorted, currentPage, pageSize);
    const purchases = paginate(
      sortedPurchase,
      currentPagePurchases,
      pageSizePurchase
    );

    return (
      <React.Fragment>
        <Modal show={openModal} onHide={this.handleCloseModal}>
          <Modal.Header closeButton>
            <Modal.Title>Archive Supplier</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            You are about to archive this supplier. Are you sure?
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={this.handleCloseModal}>
              No, cancel
            </Button>
            <Button variant="danger" onClick={this.handleConfirmDelete}>
              Yes, i'm sure
            </Button>
          </Modal.Footer>
        </Modal>
        <Modal
          show={openModalDetails}
          size="lg"
          onHide={this.handleCloseDetails}
        >
          <Modal.Header closeButton>
            <Modal.Title>Orders made to supplier</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <SupplierOrdersTable
              allPurchases={purchases}
              purchases={purchases}
              onEdit={this.handleEdit}
              onDelete={this.handleDelete}
              onView={this.handleView}
              onSort={this.handleSortPurchase}
              onSearch={this.handleSearchPurchases}
              sortColumn={sortColumnPurchase}
            />
            <Pagination
              itemsCount={count_purchases}
              pageSize={pageSizePurchase}
              currentPage={currentPagePurchases}
              onPageChange={this.handlePageChangePurchases}
            />

            {/* {purchases.map(p => (
              <div key={p.id} className="card">
                <div className="card-body">
                  <h4 className="card-title"> Order # {p.id} </h4>
                  <p className="card-text"> Status: {p.status} </p>
                  <p className="card-text">Created at: {p.createdAt}</p>
                </div>
              </div>
            ))} */}
          </Modal.Body>
        </Modal>
        <h1> Suppliers </h1> <br />
        <button className="btn btn-primary btn-sm" onClick={this.handleToggle}>
          Add Supplier
        </button>
        <br />
        <br />
        <div className="card animated fadeIn">
          <div className="card-body">
            {!this.state.inputMode && (
              <div className="table-responsive">
                <SuppliersTable
                  allSuppliers={sorted}
                  suppliers={suppliers}
                  onEdit={this.handleEdit}
                  onDelete={this.handleDelete}
                  onView={this.handleView}
                  onSort={this.handleSort}
                  onSearch={this.handleSearch}
                  sortColumn={sortColumn}
                />
                <Pagination
                  itemsCount={count}
                  pageSize={pageSize}
                  currentPage={currentPage}
                  onPageChange={this.handlePageChange}
                />
              </div>
            )}
            {this.state.inputMode && (
              <form onSubmit={this.handleSubmit}>
                <div className="row animated fadeIn">
                  <div className="col-6">
                    <div className="form-group">
                      <label> Names </label>
                      <input
                        type="text"
                        className="form-control"
                        name="names"
                        value={this.state.formData.names}
                        onChange={this.handleChange}
                        placeholder="Supplier names"
                      />
                    </div>
                    <div className="form-group">
                      <label> Contact </label>
                      <input
                        type="text"
                        className="form-control"
                        name="contact"
                        value={this.state.formData.contact}
                        onChange={this.handleChange}
                        placeholder="Contact"
                      />
                    </div>
                    <div className="form-group">
                      <label> Phone Number </label>
                      <input
                        type="text"
                        className="form-control"
                        name="phonenumber"
                        value={this.state.formData.phonenumber}
                        onChange={this.handleChange}
                        placeholder="0780..."
                      />
                    </div>
                    <div className="form-group">
                      <label> Address </label>
                      <input
                        type="text"
                        className="form-control"
                        name="address"
                        value={this.state.formData.address}
                        onChange={this.handleChange}
                        placeholder="Address"
                      />
                    </div>
                    <div className="form-group">
                      <button className="btn btn-primary btn-sm">Submit</button>
                    </div>
                  </div>
                </div>
              </form>
            )}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Suppliers;
