import React, { Component } from "react";
import { paginate } from "../../../utils/paginate";
import _ from "lodash";
import Pagination from "../../components/common/pagination";
import axios from "axios";
import url, { getRoleId } from "../../../services/globalVars";
import { Accordion, Icon, Popup, Button } from "semantic-ui-react";
import { timingSafeEqual } from "crypto";
import DetailsModal from "../../components/common/detailsModal";
import {
  ToastsContainer,
  ToastsStore,
  ToastsContainerPosition
} from "react-toasts";
import { Modal } from "react-bootstrap";
import { Card, CardBody, CardHeader, Col, Row, Table } from "reactstrap";
import { getRoleIdFromDB, getUserDetails } from "../../../services/userService";

import SalesTable from "./salesTable";
import SalesDetailsRow from "./salesDetailsRow";

class Sales extends Component {
  constructor(props) {
    super(props);
    this.state = {
      thisUser: {},
      numRows: 1,
      sales: [],
      paymentsSale: [],
      products: [],
      clients: [],
      stocks: [],
      paymentTypes: [],
      paymentType: {},
      tempPurchase: {
        productId: null,
        quantity: 0,
        unitPrice: 0,
        stockId: null
      },
      sortColumn: {
        path: "",
        label: ""
      },
      details: {},
      detailsIndiv: {},
      currentPage: 1,
      pageSize: 10,
      search: false,
      filter: "",
      inputMode: false,
      amountToPay: 0,
      formData: {
        selectedProducts: [],
        createdAt: new Date(),
        // initiatedby: 1,
        initiatedtime: this.getTime(),
        // verifiedby: {},
        verifiedTime: null,
        status: {},
        statusId: 1,
        clients: {},
        stock: {},
        client_id: "",
        stock_id: "",
        // approuved: false,
        updated_at: new Date()
      },
      selectedProducts: [],
      salesDetails: [],
      productsSold: [],
      temp_product: {},
      showDetails: false,
      showPaymentDetails: false,
      showCommentBox: false,
      showPaymentForm: false,
      detailModal: false,
      quantity_update: "",
      sales_id_update: "",
      activeIndex: -1,
      saleStatus: 0,
      amountPaid: 0,
      totalPaid: 0,
      totalDue: 0
    };

    this.handleToggle = this.handleToggle.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.getTime = this.getTime.bind(this);
    this.handleCancel = this.handleCancel.bind(this);
    this.handleVerified = this.handleVerified.bind(this);
    this.handleApprove = this.handleApprove.bind(this);
    this.handleExecuted = this.handleExecuted.bind(this);
    this.handleCloseDetailsModal = this.handleCloseDetailsModal.bind(this);
    this.handleChangeQuantity = this.handleChangeQuantity.bind(this);
    this.updateQuantity = this.updateQuantity.bind(this);
    this.getSalesDetailsIndiv = this.getSalesDetailsIndiv.bind(this);
    this.handleCloseCommentBox = this.handleCloseCommentBox.bind(this);
    this.handleCollapse = this.handleCollapse.bind(this);
    this.handleInitPayment = this.handleInitPayment.bind(this);
    this.handleClosePaymentForm = this.handleClosePaymentForm.bind(this);
    this.getSale = this.getSale.bind(this);
    this.handleChangeAmountPaid = this.handleChangeAmountPaid.bind(this);
    this.handleCheckPayment = this.handleCheckPayment.bind(this);
    this.handleClosePaymentDetailsModal = this.handleClosePaymentDetailsModal.bind(
      this
    );
  }

  getTime() {
    let today = new Date();
    return today.getTime();
  }

  componentDidMount() {
    let username = localStorage.getItem("username");
    getRoleIdFromDB(username);

    getUserDetails(username).then(d => {
      this.setState({
        thisUser: d.data
      });
    });

    //TODO get all the sales from the backend
    this.getSales();
    this.getProducts();
    this.getStatus(1);
    this.getStocks();
    this.getClient(1);
    this.getClients();
    this.getPaymentTypes();
  }

  async getProducts() {
    // Get Products
    let prodResp = await axios.get(url + "Products");
    let products = prodResp.data.content;
    // let temp_p = [
    //   { key: 1, text: "product1", value: 1 },
    //   { key: 2, text: "product2", value: 2 },
    //   { key: 3, text: "product3", value: 3 }
    // ];

    let p2 = products.map(p => {
      return {
        key: p.id,
        text: p.description,
        value: p.id
      };
    });

    this.setState({
      products: p2
    });
  }

  async getProduct(id) {
    let res = await axios.get(url + "Products/" + id);
    let temp_product = res.data;

    this.setState({
      temp_product
    });
    return res.data;
  }

  async getSalesDetails(id) {
    let res = await axios.get(url + "Salesdetails/" + id + "/sales");
    let details = res.data.content;
    this.setState({
      details
    });
  }

  async getSalesDetailsIndiv(id) {
    await axios.get(url + "Salesdetails/" + id).then(d => {
      let detailsIndiv = d.data;
      this.setState({
        detailsIndiv
      });
    });
  }

  async getSale(id) {
    await axios.get(url + "Sales/" + id).then(d => {
      let saleSingle = d.data;
      this.setState({
        saleSingle
      });
    });
  }

  async getStatus(id) {
    let res = await axios.get(url + "Status/" + id);
    let status = res.data;

    let formData = {
      ...this.state.formData
    };
    formData.status = status;
    this.setState({
      formData
    });
  }

  async getClients() {
    let res = await axios.get(url + "Clients");
    let clients = res.data.content;

    this.setState({
      clients
    });
  }

  async getStocks() {
    let res = await axios.get(url + "Stocks");
    let stocks = res.data.content;

    this.setState({
      stocks
    });
  }

  async getPaymentTypes() {
    let res = await axios.get(url + "Paymenttypes");
    let paymentTypes = res.data.content;

    this.setState({
      paymentTypes
    });
  }

  async getPaymentType(id) {
    let res = await axios.get(url + "Paymenttypes/" + id);
    let paymentType = res.data;

    this.setState({
      paymentType
    });
  }

  async getClient(id) {
    let res = await axios.get(url + "Clients/" + id);
    let clients = res.data;

    let formData = {
      ...this.state.formData
    };
    formData.clients = clients;
    this.setState({
      formData
    });
  }

  async getStock(id) {
    let res = await axios.get(url + "Stocks/" + id);
    let stock = res.data;

    let formData = {
      ...this.state.formData
    };
    formData.stock = stock;
    this.setState({
      formData
    });
  }

  async getSales() {
    let response2 = await axios.get(url + "Sales");
    let d2 = response2.data.content;

    let p = [];

    console.log(d2);

    d2.map(d => {
      let pchs = {
        id: d.id,
        createdAt: d.createdAt,
        initiatedby: d.initiatedby,
        initiatedtime: d.initiatedtime,
        verifiedby: d.verifiedby,
        verifiedTime: d.verifiedTime,
        approuvedby: d.approuvedby,
        approuvedTime: d.approuvedTime,
        executedby: d.executedby,
        executedTime: d.executedTime,
        statusLabel: d.status && d.status.status,
        status: d.status,
        statusId: d.status && d.status.id,
        retired: d.retired,
        clientNames:
          d.clients && d.clients.firstName + " " + d.clients.lastName,
        clients: d.clients,
        client_id: d.clients && d.clients.id,
        approuved: d.approved,
        updated_at: d.updated_at,
        seller: this.state.thisUser
      };

      p.push(pchs);
      this.setState({
        sales: p
      });
    });
  }

  handlePageChange = page => {
    this.setState({
      currentPage: page
    });
  };

  handleChange = ({ currentTarget: input }) => {
    const formData = {
      ...this.state.formData
    };

    if (input.name === "client_id") {
      this.getClient(input.value);
    }
    if (input.name === "stock_id") {
      this.getStock(input.value);
    }

    if (input.name === "payment_type") {
      this.getPaymentType(input.value).then(() => {
        console.log(this.state.paymentType);
      });
    }

    formData[input.name] = input.value;
    this.setState({
      formData
    });
    // console.log(this.state.formData);
  };

  handleChange2 = ({ currentTarget: input }) => {
    let purc = {
      ...this.state.tempPurchase
    };
    if (input.id === "productId") {
      this.getProduct(input.value).then(d => {
        purc.product = d;
        purc[input.id] = input.value;
      });
    } else {
      purc[input.id] = input.value;
    }

    console.log(purc);
    this.setState({
      tempPurchase: purc
    });
  };

  handleDelete = sale => {
    // console.log(sale);
    // deletestore(sale);
    let sales = {
      ...this.state.sales
    };
    sales = _.filter(sales, p => {
      return p.id !== sale.id;
    });

    //TODO Send delete request to the backend
    axios
      .delete(url + "Sales/" + sale.id, { validateStatus: false })
      .then(d => {
        if (d.status === 200) {
          ToastsStore.success("Success! " + d.statusText);
          this.setState({
            sales
          });
          this.getSales();
        } else {
          ToastsStore.error("Error! " + d.data.message);
        }
      });
  };

  handleView = sale => {
    console.log(sale);
    this.setState({
      showDetails: true
    });
    this.getSalesDetails(sale.id).then(d => {
      console.log(d);
      let productsSold = this.state.details;
      this.setState({
        productsSold
      });
    });

    this.getSale(sale.id).then(d => {
      console.log(this.state.saleSingle.status.id);
      let saleStatus = this.state.saleSingle.status.id;
      this.setState({
        saleStatus
      });
    });
  };

  handleCloseDetailsModal() {
    this.setState({
      showDetails: false
    });
  }

  handleClosePaymentDetailsModal() {
    this.setState({
      showPaymentDetails: false
    });
  }

  //TODO send put request to the backend
  handleEdit = sale => {
    console.log(sale);

    let p = {
      ...sale
    };

    p.Location = "New Location";
    console.log(p);
  };

  handleSort = sortColumn => {
    this.setState({
      sortColumn
    });
  };
  handleSearch = item => {
    this.setState({
      search: true,
      filter: item
    });
  };
  handleToggle = () => {
    this.setState({
      inputMode: !this.state.inputMode,
      numRows: 1
    });
  };

  async handleSubmit(e) {
    e.preventDefault();
    this.handleAddChild();

    // console.log(this.state.formData);
    await axios.post(url + "Sales", this.state.formData).then(d => {
      console.log(this.state.salesDetails);
      if (d.status === 200) {
        for (var i = 0; i < this.state.salesDetails.length; i++) {
          let pd = {
            sales_id: d.data.id,
            sales: d.data,
            product: this.state.salesDetails[i].product,
            unitprice: this.state.salesDetails[i].product.unitsalesprice,
            quantity: this.state.salesDetails[i].quantity,
            seller_id: localStorage.getItem("roleId"),
            stock: this.state.formData.stock
            // unitprice: this.state.temp_product.unitpurchasecost,
            // client: this.state.formData.client
          };

          {
            if (pd.sales_id) {
              console.log(pd);
              axios
                .post(url + "Salesdetails", pd, { validateStatus: false })
                .then(e => {
                  if (e.status === 200) {
                    ToastsStore.success("Success! " + e.statusText);
                    let salesDetails = [];
                    let formData = {
                      ...this.state.formData
                    };

                    formData.client_id = "";
                    this.setState({
                      salesDetails,
                      formData,
                      numRows: 1
                    });
                  } else {
                    axios.delete(url + "Sales/" + d.data.id).then(() => {
                      ToastsStore.error("Error! " + e.data.message);
                      this.getSales();
                    });
                  }
                });
            }
          }
        }
      } else {
        ToastsStore.error("Error! " + d.data.message);
      }

      this.getSales();
      this.handleToggle();
    });
  }

  handleAddChild = () => {
    const { productId, quantity, unitPrice, stockId } = {
      ...this.state.tempPurchase
    };
    // console.log(this.state.tempPurchase);

    {
      if (productId && quantity) {
        this.setState({
          numRows: this.state.numRows + 1
        });
        this.state.salesDetails.push(this.state.tempPurchase);

        let tempPurchase = {};
        this.setState({
          tempPurchase
        });
      }
    }

    console.log(this.state.salesDetails);
  };

  handleRemoveChild = () => {
    console.log("Delete");
  };

  async handleCancel(sale) {
    let p = {
      ...sale
    };
    p.status = {
      id: 5,
      status: "cancelled"
    };

    this.setState({
      showCommentBox: true
    });

    console.log(p);

    await axios.post(url + "Sales", p, { validateStatus: false }).then(d => {
      if (d.status === 200) {
        ToastsStore.success("Success! " + d.statusText);
        this.getSales();
      } else {
        ToastsStore.error("Error! " + d.data.message);
      }
    });
  }

  handleCloseCommentBox() {
    this.setState({
      showCommentBox: false
    });
  }

  async handleVerified(sale) {
    console.log(sale);
    let p = {
      ...sale
    };
    p.status = {
      id: 2,
      status: "verified"
    };

    await axios.post(url + "Sales", p, { validateStatus: false }).then(d => {
      if (d.status === 200) {
        ToastsStore.success("Success! " + d.statusText);
        this.getSales();
      } else {
        ToastsStore.error("Error! " + d.data.message);
      }
    });
  }

  async handleApprove(sale) {
    console.log(sale);
    let p = {
      ...sale
    };

    p.status = {
      id: 3,
      status: "approved"
    };

    console.log(p);

    await axios.post(url + "Sales", p, { validateStatus: false }).then(d => {
      if (d.status === 200) {
        ToastsStore.success("Success! " + d.statusText);
        this.getSales();
      } else {
        ToastsStore.error("Error! " + d.data.message, 10000);
      }
    });
  }

  async handleExecuted(sale) {
    console.log(sale);
    let p = {
      ...sale
    };
    p.status = {
      id: 4,
      status: "executed"
    };

    await axios.put(url + "Sales", p, { validateStatus: false }).then(d => {
      if (d.status === 200) {
        ToastsStore.success("Success! " + d.statusText);
        this.getSales();
      } else {
        ToastsStore.error("Error! " + d.data.message);
      }
      console.log(d);
    });
  }

  handleChangeQuantity = ({ currentTarget: input }) => {
    this.setState({
      quantity_update: input.value,
      sales_id_update: input.id
    });
  };

  async updateQuantity({ currentTarget: input }) {
    if (input.id === "b" + this.state.sales_id_update) {
      this.getSalesDetailsIndiv(this.state.sales_id_update).then(() => {
        console.log(this.state.detailsIndiv);
        let d = {
          ...this.state.detailsIndiv
        };
        d.quantity = this.state.quantity_update;

        if (this.state.quantity_update === "0") {
          axios
            .delete(url + "Salesdetails/" + this.state.sales_id_update)
            .then(() => {
              this.setState({
                showDetails: false
              });
            });
        } else {
          axios.post(url + "Salesdetails", d).then(() => {
            this.setState({
              showDetails: false
            });
          });
        }
      });
    }
  }

  handleCollapse = (e, titleProps) => {
    const { index } = titleProps;
    const { activeIndex } = this.state;
    const newIndex = activeIndex === index ? -1 : index;

    this.setState({
      activeIndex: newIndex
    });
  };

  handleInitPayment = sale => {
    axios.get(url + "Sales/total/" + sale.id).then(d => {
      console.log(d);
      this.setState({
        amountToPay: d.data
      });
    });

    this.setState({
      showPaymentForm: true,
      selectedSale: sale
    });

    console.log(sale);
  };

  handleCheckPayment = sale => {
    axios.get(url + "Payments/amount/" + sale.id).then(d => {
      let totalPaid = d.data;
      console.log(totalPaid);
      this.setState({
        totalPaid,
        selectedSale: sale
      });

      axios.get(url + "Sales/total/" + sale.id).then(e => {
        let totalDue = e.data;
        console.log(totalDue);
        this.setState({
          totalDue
        });
        axios.get(url + "Payments/sales/" + sale.id).then(f => {
          let paymentsSale = f.data.content;
          console.log(paymentsSale);
          this.setState({
            paymentsSale,
            showPaymentDetails: true
          });
        });
      });
    });
  };

  handleClosePaymentForm = calc => {
    this.setState({
      showPaymentForm: !this.state.showPaymentForm
    });

    if (calc) {
      let paymentObj = {
        // id: 1,
        paidamount: this.state.amountPaid,
        paymentsdate: Date.now(),
        clients: this.state.selectedSale.clients,
        paymenttype: this.state.paymentType,
        users: this.state.thisUser,
        status: {
          id: 1,
          status: "initiated"
        },
        sales: this.state.selectedSale
      };

      this.submitPayment(paymentObj).then(d => {
        this.getSales();
        // console.log(d);
      });
    }
  };

  async submitPayment(paymentObj) {
    await axios.post(url + "Payments", paymentObj);
  }

  handleChangeAmountPaid = ({ currentTarget: input }) => {
    this.setState({
      amountPaid: input.value
    });
  };

  handleApprovePayment = p => {
    console.log(p);
    let payment = p;
    payment.status = {
      id: 3,
      status: "approved"
    };

    axios.post(url + "Payments", payment, { validateStatus: false }).then(d => {
      if (d.status === 200) {
        ToastsStore.success("Success! " + d.statusText);
        this.handleCheckPayment(this.state.selectedSale);
      } else {
        ToastsStore.error("Error! " + d.data.message);
      }
    });
  };

  handleCancelPayment = p => {
    let payment = p;
    payment.status = {
      id: 5,
      status: "rejected"
    };

    axios.post(url + "Payments", payment, { validateStatus: false }).then(d => {
      if (d.status === 200) {
        ToastsStore.success("Success! " + d.statusText);
        this.handleCheckPayment(this.state.selectedSale);
      } else {
        ToastsStore.error("Error! " + d.data.message);
      }
    });
  };
  render() {
    const getBadge = status => {
      return status === "approved"
        ? "badge badge-success"
        : status === "rejected"
        ? "badge badge-danger"
        : status === "initiated"
        ? "badge badge-info"
        : status === "Banned"
        ? "danger"
        : "primary";
    };

    const {
      sales: allSales,
      sortColumn,
      pageSize,
      currentPage,
      search,
      filter,
      clients,
      products,
      stocks,
      showDetails,
      showPaymentDetails,
      productsSold,
      showCommentBox,
      activeIndex,
      saleStatus,
      showPaymentForm,
      paymentTypes,
      amountToPay,
      paymentsSale,
      totalPaid,
      totalDue
    } = this.state;

    let searched = [];

    if (search && filter !== "") {
      const data = allSales;
      let data_array = Object.values(data);
      let searchString = filter;
      searched = data_array.filter(element => {
        for (var property in element) {
          if (element.hasOwnProperty(property)) {
            if (
              element[property]
                .toString()
                .toLowerCase()
                .includes(searchString.toLowerCase())
            ) {
              return true;
            }
          }
        }
        return null;
      });
    } else {
      searched = allSales;
    }

    const sorted = _.orderBy(searched, [sortColumn.path], [sortColumn.order]);

    const count = searched.length;

    const sales = paginate(sorted, currentPage, pageSize);

    const children = [];
    for (var i = 0; i < this.state.numRows; i++) {
      children.push(
        <SalesDetailsRow
          key={i}
          number={i}
          onRemove={this.handleRemoveChild}
          products={products}
          stocks={stocks}
          onChange={this.handleChange2}
        />
      );
    }

    const roleId = getRoleId();

    return (
      <React.Fragment>
        <ToastsContainer
          store={ToastsStore}
          position={ToastsContainerPosition.TOP_RIGHT}
          lightBackground
        />
        <Modal
          show={showPaymentForm}
          onHide={() => this.handleClosePaymentForm(false)}
        >
          <Modal.Header closeButton>
            <Modal.Title>Payment Information</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div className="col-8">
              <div className="form-group">
                <label>Amount Due:</label>
                <input
                  type="number"
                  className="form-control"
                  name="amount_due"
                  value={amountToPay}
                  readOnly
                  // onChange={this.handleChangeQuantity}
                />
              </div>
              <div className="form-group">
                <label>Amount Paid:</label>
                <input
                  type="number"
                  className="form-control"
                  name="amount_paid"
                  onChange={this.handleChangeAmountPaid}
                />
              </div>

              <div className="form-group">
                <label> Payment Type </label>
                <select
                  className="form-control"
                  name="payment_type"
                  onChange={this.handleChange}
                >
                  <option value=""> --Select Payment Type-- </option>
                  {paymentTypes.map(s => (
                    <option key={s.id} value={s.id}>
                      {s.paymenttypename}
                    </option>
                  ))}
                </select>
              </div>
              <div className="form-group">
                <button
                  className="btn btn-sm btn-primary"
                  onClick={() => this.handleClosePaymentForm(true)}
                >
                  Submit
                </button>
              </div>
            </div>
          </Modal.Body>
        </Modal>
        <Modal show={showDetails} onHide={this.handleCloseDetailsModal}>
          <Modal.Header closeButton>
            <Modal.Title> Sales Details </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Accordion>
              {productsSold.map((p, i) => (
                <div key={p.id}>
                  <Accordion.Title
                    active={activeIndex === i}
                    index={i}
                    onClick={this.handleCollapse}
                  >
                    <Icon name="dropdown" /> {p.product.productcode}
                  </Accordion.Title>
                  <Accordion.Content active={activeIndex === i}>
                    <div key={p.id} className="card">
                      <div className="card-body">
                        <p className="card-text"> Quantity: {p.quantity} </p>

                        {(roleId === "4" ||
                          roleId === "5" ||
                          roleId === "7" ||
                          roleId === "2") &&
                          saleStatus < 2 && (
                            <div>
                              <p>
                                Unit Cost:
                                <strong> {p.product.unitpurchasecost} </strong>
                                RWF
                              </p>
                              <div className="row">
                                <div className="col-6">
                                  <div className="form-group">
                                    <label> Adjust the quantity </label>
                                    <input
                                      type="text"
                                      className="form-control"
                                      name="quantity_update"
                                      id={p.id}
                                      onChange={this.handleChangeQuantity}
                                    />
                                  </div>
                                  <div className="form-group">
                                    <button
                                      id={"b" + p.id}
                                      className="btn btn-sm btn-primary"
                                      onClick={this.updateQuantity}
                                    >
                                      Submit
                                    </button>
                                  </div>
                                </div>
                              </div>
                            </div>
                          )}
                      </div>
                    </div>
                  </Accordion.Content>
                </div>
              ))}
            </Accordion>
          </Modal.Body>
        </Modal>
        <Modal
          show={showPaymentDetails}
          onHide={this.handleClosePaymentDetailsModal}
        >
          <Modal.Header closeButton>
            <Modal.Title> Payment Details </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <h3>Total Paid: {totalPaid}</h3>
            <h3>Total Due: {totalDue}</h3>
            <Accordion>
              {paymentsSale.map((p, i) => (
                <div key={p.id}>
                  <Accordion.Title
                    active={activeIndex === i}
                    index={i}
                    onClick={this.handleCollapse}
                  >
                    <Icon name="dropdown" />
                    <div className={getBadge(p.status.status)}>
                      Payment #{p.id} - {p.status.status}
                    </div>
                  </Accordion.Title>
                  <Accordion.Content active={activeIndex === i}>
                    <div key={p.id} className="card">
                      <div className="card-body">
                        <label />
                        <br />
                        <table>
                          <tbody>
                            <tr>
                              <td width="80%">Amount Paid: {p.paidamount}</td>
                              <td>
                                <Button.Group size="mini">
                                  {p.status.id !== 3 && (
                                    <Popup
                                      trigger={
                                        <Button
                                          basic
                                          color="green"
                                          icon="check"
                                          onClick={() =>
                                            this.handleApprovePayment(p)
                                          }
                                        />
                                      }
                                      content="Approve"
                                    />
                                  )}
                                  {p.status.id !== 3 && p.status.id !== 5 && (
                                    <Popup
                                      trigger={
                                        <Button
                                          basic
                                          color="red"
                                          icon="cancel"
                                          onClick={() =>
                                            this.handleCancelPayment(p)
                                          }
                                        />
                                      }
                                      content="Ignore"
                                    />
                                  )}
                                </Button.Group>
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </Accordion.Content>
                </div>
              ))}
            </Accordion>
          </Modal.Body>
        </Modal>
        <Modal show={showCommentBox} onHide={this.handleCloseCommentBox}>
          <Modal.Header closeButton>
            <Modal.Title>Please provide comments for the rejection</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div className="col-8">
              <div className="form-group">
                <textarea className="form-control" name="comments" />
              </div>
              <div className="form-group">
                <button
                  className="btn btn-sm btn-primary"
                  onClick={this.handleCloseCommentBox}
                >
                  Submit
                </button>
              </div>
            </div>
          </Modal.Body>
          {/* <Modal.Footer>
                                        <Button variant="secondary" onClick={this.handleCloseDetailsModal}>
                                          No, Cancel
                                        </Button>
                                        <Button variant="danger" onClick={this.handleConfirmDelete}>
                                          Yes, Delete
                                        </Button>
                                      </Modal.Footer> */}
        </Modal>
        <h1> Sales </h1> <br />
        {roleId === "3" && (
          <button
            className="btn btn-primary btn-sm"
            onClick={this.handleToggle}
          >
            Add Sales
          </button>
        )}
        <br />
        <br />
        <div className="card animated fadeIn">
          <div className="card-body">
            {!this.state.inputMode && (
              <div className="table-responsive">
                <SalesTable
                  allSales={sorted}
                  sales={sales}
                  onEdit={this.handleEdit}
                  onDelete={this.handleDelete}
                  onView={this.handleView}
                  onSort={this.handleSort}
                  onSearch={this.handleSearch}
                  sortColumn={sortColumn}
                  onCancel={this.handleCancel}
                  onVerified={this.handleVerified}
                  onApprove={this.handleApprove}
                  onExecuted={this.handleExecuted}
                  onInitPayment={this.handleInitPayment}
                  onCheckPayment={this.handleCheckPayment}
                  roleId={roleId}
                />
                <Pagination
                  itemsCount={count}
                  pageSize={pageSize}
                  currentPage={currentPage}
                  onPageChange={this.handlePageChange}
                />
              </div>
            )}
            {this.state.inputMode && (
              <form>
                <div className="row animated fadeIn">
                  <div className="col-12">
                    <div className="col-4">
                      <div className="form-group">
                        <label> Client </label>
                        <select
                          className="form-control"
                          name="client_id"
                          value={this.state.formData.client_id}
                          onChange={this.handleChange}
                        >
                          <option value=""> --Select Client-- </option>
                          {clients.map(s => (
                            <option key={s.id} value={s.id}>
                              {s.firstName} {s.lastName}
                            </option>
                          ))}
                        </select>
                      </div>
                    </div>
                    <div className="col-4">
                      <div className="form-group">
                        <label> Stock </label>
                        <select
                          className="form-control"
                          name="stock_id"
                          value={this.state.formData.stock_id}
                          onChange={this.handleChange}
                          required
                        >
                          <option value=""> --Select Stock-- </option>
                          {stocks.map(s => (
                            <option key={s.id} value={s.id}>
                              {s.description}
                            </option>
                          ))}
                        </select>
                      </div>
                    </div>
                    <div className="form-group">
                      <button
                        type="button"
                        className="btn btn-sm btn-warning"
                        onClick={this.handleAddChild}
                      >
                        +
                      </button>
                      <button
                        className="btn btn-sm btn-danger ml-2"
                        type="button"
                        onClick={this.handleRemoveChild}
                      >
                        x
                      </button>
                    </div>
                    {children}
                    <div className="form-group">
                      <button
                        className="btn btn-primary btn-sm"
                        onClick={this.handleSubmit}
                      >
                        Submit
                      </button>
                    </div>
                  </div>
                </div>
              </form>
            )}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Sales;
