import React, { Component } from "react";

import Pagination from "../../components/common/pagination";
import { paginate } from "../../../utils/paginate";
import {
  getProducts,
  deleteProduct,
  getBrands
} from "../../../services/productsServices";
import BrandsTable from "././brandsTable";
import _ from "lodash";
import axios from "axios";

import { Modal, Button } from "react-bootstrap";
import { Header, Icon, Image } from "semantic-ui-react";

import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import url from "./../../../services/globalVars";

const $ = require("jquery");

const style_width = {
  width: "400px",
  margin: "300px"
};

class Brands extends Component {
  constructor(props) {
    super(props);
    this.state = {
      openModal: false,
      brands: [],
      pageSize: 10,
      currentPage: 1,
      sortColumn: {
        path: "product_code",
        order: "asc"
      },
      search: false,
      filter: "",
      inputMode: false,
      formData: {
        name: "",
        description: "",
        manufacturer: ""
      },
      brandToDelete: {}
    };

    this.handleToggle = this.handleToggle.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.toggleCheck = this.toggleCheck.bind(this);
    this.handleCloseModal = this.handleCloseModal.bind(this);
    this.handleConfirmDelete = this.handleConfirmDelete.bind(this);
  }

  componentDidMount() {
    // Get Brands
    this.getBrands();
  }

  async getBrands() {
    let response = await axios.get(url + "Brands");
    let d = response.data.content;
    this.setState({
      brands: d
    });
  }

  async getProducts() {
    // Get Brands
    let prodResp = await axios.get(url + "Brands");
    let brands = prodResp.data.content;

    let p2 = brands.map(p => {
      return {
        id: p.id,
        productcode: p.productcode,
        description: p.description,
        quantityBalance: p.quantityBalance,
        brand: p.brand.name,
        brandId: p.brand.id
      };
    });

    this.setState({
      brands: p2
    });
  }

  handleToggle() {
    this.setState({
      inputMode: !this.state.inputMode
    });
  }

  handlePageChange = page => {
    this.setState({
      currentPage: page
    });
  };

  handleEdit = brand => {
    console.log(brand);
    //TODO update brand
  };

  handleDelete = brand => {
    this.setState({
      openModal: true,
      brandToDelete: brand
    });
  };

  handleCloseModal() {
    this.setState({
      openModal: false
    });
  }

  handleConfirmDelete() {
    let brands = {
      ...this.state.brands
    };

    axios.delete(url + "Brands/" + this.state.brandToDelete.id, {}).then(() => {
      brands = _.filter(brands, p => {
        return p.id !== this.state.brandToDelete.id;
      });
      this.setState({
        brands
      });
      this.getBrands();
    });

    this.setState({
      openModal: false
    });
  }

  handleSort = sortColumn => {
    this.setState({
      sortColumn
    });
  };

  handleSearch = item => {
    this.setState({
      search: true,
      filter: item
    });
  };

  handleDateChange = date => {
    this.setState({
      startDate: date
    });

    let formData = {
      ...this.state.formData
    };
    formData["expireDate"] = date;
    this.setState({
      formData
    });
  };

  handleChange = ({ currentTarget: input }) => {
    const formData = {
      ...this.state.formData
    };
    formData[input.name] = input.value;
    this.setState({
      formData
    });
  };

  toggleCheck() {
    let formData = {
      ...this.state.formData
    };
    formData["retired"] = !this.state.formData.retired;
    this.setState({
      formData
    });
  }

  async handleSubmit(e) {
    e.preventDefault();

    // let id = parseInt(Math.random()) + 100;
    // this.state.formData.id = id;

    console.log(this.state.formData);
    await axios.post(url + "Brands", this.state.formData);

    this.setState({
      inputMode: !this.state.inputMode
    });

    this.getBrands();

    let formData = {};
    this.setState({
      formData
    });
  }

  render() {
    const {
      pageSize,
      currentPage,
      brands: allBrands,
      sortColumn,
      search,
      filter,
      openModal
    } = this.state;

    var searched = [];

    if (search && filter !== "") {
      const data = allBrands;
      let data_array = Object.values(data);
      let searchString = filter;
      searched = data_array.filter(element => {
        for (var property in element) {
          if (element.hasOwnProperty(property)) {
            if (
              element[property]
                .toString()
                .toLowerCase()
                .includes(searchString.toLowerCase())
            ) {
              return true;
            }
          }
        }
        return null;
      });
    } else {
      searched = allBrands;
    }
    const sorted = _.orderBy(searched, [sortColumn.path], [sortColumn.order]);

    const count = searched.length;

    const brands = paginate(sorted, currentPage, pageSize);

    return (
      <React.Fragment>
        <Modal show={openModal} onHide={this.handleCloseModal}>
          <Modal.Header closeButton>
            <Modal.Title>Archive Brand</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            You are about to archive this brand. Are you sure?
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={this.handleCloseModal}>
              No, cancel
            </Button>
            <Button variant="danger" onClick={this.handleConfirmDelete}>
              Yes, i'm sure
            </Button>
          </Modal.Footer>
        </Modal>
        <h1> Brands </h1> <br />
        <button className="btn btn-primary btn-sm" onClick={this.handleToggle}>
          Add Brand{" "}
        </button>{" "}
        <br />
        <br />
        <div className="card animated fadeIn">
          <div className="card-body">
            {" "}
            {!this.state.inputMode && (
              <div className="table-responsive">
                <BrandsTable
                  allBrands={sorted}
                  brands={brands}
                  onEdit={this.handleEdit}
                  onDelete={this.handleDelete}
                  onSort={this.handleSort}
                  onSearch={this.handleSearch}
                  sortColumn={sortColumn}
                />{" "}
                <Pagination
                  itemsCount={count}
                  pageSize={pageSize}
                  currentPage={currentPage}
                  onPageChange={this.handlePageChange}
                />{" "}
              </div>
            )}{" "}
            {this.state.inputMode && (
              <form onSubmit={this.handleSubmit}>
                <div className="row animated fadeIn">
                  <div className="col-6">
                    <div className="form-group">
                      <label> Brand Name </label>{" "}
                      <input
                        type="text"
                        className="form-control"
                        name="name"
                        value={this.state.formData.name}
                        onChange={this.handleChange}
                        placeholder="Brand name"
                      />
                    </div>
                    <div className="form-group">
                      <label> Description </label>{" "}
                      <input
                        type="text"
                        className="form-control"
                        name="description"
                        value={this.state.formData.description}
                        onChange={this.handleChange}
                        placeholder="Description"
                      />
                    </div>
                    <div className="form-group">
                      <label> Manufacturer </label>{" "}
                      <input
                        type="text"
                        className="form-control"
                        name="manufacturer"
                        value={this.state.formData.manufacturer}
                        onChange={this.handleChange}
                        placeholder="Manufacturer"
                      />
                    </div>
                    <div className="form-group">
                      <button className="btn btn-primary btn-sm">
                        {" "}
                        Submit{" "}
                      </button>{" "}
                    </div>{" "}
                  </div>{" "}
                </div>{" "}
              </form>
            )}{" "}
          </div>{" "}
        </div>{" "}
      </React.Fragment>
    );
  }
}

export default Brands;
