import React, { Component } from "react";
import TableBody from "./tableBody";
import TableHeader from "./tableHeader";
import TableSearch from "./tableSearch";

class Table extends Component {
  render() {
    const { columns, sortColumn, onSort, onSearch, data, allData } = this.props;
    return (
      <React.Fragment>
        <TableSearch data={allData} onSearch={onSearch} columns={columns} />

        <br />
        <table className="table table-condensed table-hover table-sm animated fadeIn table-bordered">
          <TableHeader
            columns={columns}
            sortColumn={sortColumn}
            onSort={onSort}
          />
          <TableBody data={data} columns={columns} />
        </table>
      </React.Fragment>
    );
  }
}

export default Table;
