import React, { Component } from "react";
import Table from "../../components/common/table";

class BrandsTable extends Component {
  columns = [
    { path: "name", lable: "Name" },
    { path: "description", lable: "Description" },
    // { path: "manufacturer", lable: "Manufacturer" },
    {
      key: "edit",
      lable: "Actions",
      content: brand => (
        <React.Fragment>
          <div className="row">
            <button
              className="btn btn-sm btn-primary mr-2 ml-3"
              onClick={() => this.props.onEdit(brand)}
            >
              <i className="cui-pencil icons font-lg" />
            </button>

            <button
              className="btn btn-sm btn-danger"
              onClick={() => this.props.onDelete(brand)}
            >
              <i className="cui-trash icons font-lg " />
            </button>
          </div>
        </React.Fragment>
      )
    }
  ];

  render() {
    const { brands, onSort, onSearch, sortColumn, allBrands } = this.props;
    return (
      <Table
        columns={this.columns}
        sortColumn={sortColumn}
        onSort={onSort}
        onSearch={onSearch}
        data={brands}
        allData={allBrands}
      />
    );
  }
}

export default BrandsTable;
